/* Start Categories links */
.categories-links {
  background-color: #fff;

  .links {
    margin-top: 45px;

    li {
      display: inline-block;

      &.active {
        background-color: rgba($primaryColor, 0.05);

        a {
          color: $primaryColor;
        }
      }

      a {
        display: block;
        padding: 8px 19px;
        font-size: 14px;
        line-height: 17px;
        font-family: "AvertaSemibold";
        transition: color 0.3s ease-in-out;

        &:hover {
          color: $primaryColor;
        }
      }
    }
  }

  .dropdown {
    margin-top: 16px;

    @media #{$tablet} {
      margin-top: 20px;
    }

    .all-articles {
      background: rgba($primaryColor, 0.05);
      font-size: 13px;
      color: $primaryColor;
      border: 0;
      position: relative;
      padding-left: 12px;
      line-height: 40px;
      height: 40px;
      width: 125px;
      font-family: "AvertaSemibold";
      text-align: left;

      @media #{$tablet} {
        line-height: 50px;
        height: 50px;
        width: 150px;
        font-size: 15px;
        padding-left: 20px;
      }

      &:after {
        color: $primaryColor;
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
        font-weight: bold;

        @media #{$tablet} {
          right: 20px;
        }
      }
    }
  }

  form {
    margin-top: 32px;
    position: relative;
    margin-bottom: 20px;
    border: 1px solid #ebe8df;
    width: 286px;

    @media #{$mobile} {
      width: 125px;
      height: 40px;
      line-height: 40px;
      margin-top: 16px;
    }

    @media #{$tablet} {
      width: 200px;
      height: 50px;
      line-height: 50px;
      margin-top: 20px;
    }

    input {
      padding: 13px 24px;
      width: 100%;
      display: block;
      border: 0;

      @media #{$mobile} {
        padding: 0 16px;
        height: 100%;

        font-size: 13px;

        &::placeholder {
          font-size: 13px;
        }
      }

      @media #{$tablet} {
        padding: 0 20px;
        height: 100%;

        font-size: 15px;

        &::placeholder {
          font-size: 15px;
        }
      }
    }

    button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      border: 0;
      width: 52px;
      height: 100%;
      background-color: #fff;

      @media #{$mobile} {
        width: 40px;
      }

      @media #{$tablet} {
        width: 50px;
      }
    }
  }
}

/* End Categories links */

/* Start Featured Articles */
.featured-articles {
  padding-bottom: 105px;

  @media #{$mobile} {
    padding-bottom: 40px;
  }

  @media #{$tablet} {
    padding-bottom: 60px;
  }

  .padd {
    padding-left: 5px;

    @media #{$maxTab} {
      padding-left: 15px;
    }
  }

  h2 {
    padding-top: 60px;
    margin-bottom: 40px;

    @media #{$mobile} {
      padding-top: 37px;
      margin-bottom: 20px;
    }
  }

  .article {
    box-shadow: 0px 10px 20px rgba(240, 238, 233, 0.4);
    padding: 52px 48px;
    margin-bottom: 70px;
    background-color: #fff;

    @media #{$mobile} {
      padding: 24px;
      margin-bottom: 10px;
    }

    @media #{$betweentabdesk} {
      min-height: 500px;
      max-height: 500px;
    }

    @media #{$tablet} {
      margin-bottom: 20px;
    }

    .label {
      background-color: rgba(#fd3e72, 0.05);
      color: $primaryColor;
      padding: 8px 19px;
      display: inline-block;
      font-family: "AvertaSemibold";
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      &:hover {
        color: #fff;
        background-color: $primaryColor;
      }

      @media #{$mobile} {
        font-size: 12px;
        padding: 3px 12px;
      }
    }

    .article-title {
      font-size: 32px;
      line-height: 36px;
      margin-top: 18px;
      margin-bottom: 20px;

      @media #{$mobile} {
        font-size: 16px;
        line-height: 20px;
        margin-top: 12px;
        margin-bottom: 10px;
        width: 80%;
      }

      a {
        transition: color 0.3s ease-in-out;

        &:hover {
          color: $primaryColor;
        }
      }
    }

    .metabox {
      font-size: 14px;
      line-height: 14px;
      color: #8d8b92;
      margin-bottom: 30px;

      @media #{$mobile} {
        font-size: 12px;
        margin-bottom: 14px;
      }

      .author-img {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-image: url(../imgs/author1.png);
        position: relative;
        top: 5px;
        margin-right: 5px;

        @media #{$mobile} {
          top: 8px;
        }
      }

      a {
        color: inherit;
        transition: color 0.3s ease-in-out;

        &:hover {
          color: $primaryColor;
        }
      }
    }

    .article-text {
      font-size: 14px;
      line-height: 28px;
      margin-bottom: 30px;

      @media #{$mobile} {
        font-size: 13px;
        line-height: 20px;
        margin-bottom: 8px;
        width: 95%;
      }
    }

    .learn-more {
      color: $primaryColor;
      text-decoration: underline !important;
      transition: all 0.3s ease-in-out;

      @media #{$mobile} {
        font-size: 14px;
      }

      i {
        margin-left: 5px;
        font-weight: bold;
        position: relative;
        transition: all 0.3s ease-in-out;
      }

      &:hover {
        text-decoration: none !important;
        color: #e33866;

        i {
          transform: translateX(3px);
        }
      }
    }
  }

  .small-article {
    border: 1px solid #e0ded5;
    box-shadow: 0px 10px 20px rgba(240, 238, 233, 0.4);
    padding: 33px 32px;

    @media #{$mobile} {
      padding: 24px;
    }

    &.first {
      margin-bottom: 20px;

      @media #{$mobile} {
        margin-bottom: 10px;
      }
    }

    .label {
      background-color: rgba(#fd3e72, 0.05);
      color: $primaryColor;
      padding: 3px 15px;
      display: inline-block;
      font-size: 13px;
      font-family: "AvertaSemibold";
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      &:hover {
        color: #fff;
        background-color: $primaryColor;
      }

      @media #{$mobile} {
        font-size: 12px;
        padding: 3px 14px;
      }
    }

    .title {
      font-size: 20px;
      line-height: 20px;
      margin: 10px 0;

      @media #{$mobile} {
        font-size: 16px;
        margin: 9px 0 7px;
      }

      a {
        transition: color 0.3s ease-in-out;

        &:hover {
          color: $primaryColor;
        }
      }
    }

    .meta-box {
      font-size: 13px;
      line-height: 14px;
      color: #8d8b92;
      margin-bottom: 15px;

      @media #{$mobile} {
        font-size: 12px;
        margin-bottom: 0;
      }

      .author-img {
        margin-right: 5px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        overflow: hidden;
        background-image: url(../imgs/author2.png);
        position: relative;
        top: 5px;

        &.two {
          background-image: url(../imgs/author3.png);
        }
      }

      a {
        color: inherit;
        transition: color 0.3s ease-in-out;

        &:hover {
          color: $primaryColor;
        }
      }
    }

    .text {
      font-size: 14px;
      line-height: 24px;
    }
  }

  .popular-articles {
    background: #ffffff;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    padding: 39px;

    @media #{$mobile} {
      padding: 24px 20px 24px 24px;
    }

    @media #{$betweentabdesk} {
      min-height: 500px;
      max-height: 500px;
    }

    .item {
      margin-bottom: 30px;

      @media #{$mobile} {
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .label {
        background-color: rgba(#fd3e72, 0.05);
        color: $primaryColor;
        padding: 3px 15px;
        display: inline-block;
        font-size: 13px;
        font-family: "AvertaSemibold";
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
          color: #fff;
          background-color: $primaryColor;
        }

        @media #{$mobile} {
          font-size: 12px;
        }
      }

      .title {
        font-size: 20px;
        line-height: 20px;
        margin: 10px 0;

        @media #{$mobile} {
          font-size: 16px;
          margin: 9px 0 7px;
        }

        @media #{$betweentabdesk} {
          font-size: 16px;
          margin: 20px 0;
        }

        a {
          transition: color 0.3s ease-in-out;

          &:hover {
            color: $primaryColor;
          }
        }
      }

      .metabox {
        font-size: 13px;
        line-height: 14px;
        color: #8d8b92;

        @media #{$mobile} {
          font-size: 12px;
        }

        a {
          color: inherit;
          transition: color 0.3s ease-in-out;

          &:hover {
            color: $primaryColor;
          }
        }
      }
    }
  }
}

/* End Featured Articles */