.hero-img.vpn {
  padding-bottom: 260px;

  @media #{$mobile} {
    text-align: center;
    padding-bottom: 60px;
  }

  @media #{$tablet} {
    padding-bottom: 140px;
  }

  .hero-img-content {
    .hero-img-title {
      margin-bottom: 30px;
      margin-top: 190px;

      @media #{$mobile} {
        margin-bottom: 25px;
        margin-top: 25px;
      }

      @media #{$tablet} {
        margin-bottom: 30px;
        margin-top: 100px;
        width: 100%;
        font-size: 42px;
      }

      @media #{$betweentabdesk} {
        margin-top: 100px;
      }
    }

    p {
      font-size: 18px;
      line-height: 28px;
      width: 86%;
      margin-bottom: 35px;

      @media #{$mobile} {
        width: 100%;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 25px;
        padding-left: 7px;
        padding-right: 7px;
      }

      @media #{$tablet} {
        width: 100%;
      }
    }
  }

  .hero-img-image {
    img {
      left: 0;
    }

    @media #{$tablet} {
      text-align: center;
    }

    @media #{$betweentabdesk} {
      img {
        top: 100px;
      }
    }
  }
}

.advantages.vpn {
  padding-top: 335px;

  @media #{$mobile} {
    padding-top: 185px;
  }

  @media #{$tablet} {
    padding-top: 250px;
  }

  .title-vpn {
    font-size: 56px;
    line-height: 56px;
    font-family: "AvertaBold";

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      width: 80%;
      margin: auto;
    }

    @media #{$tablet} {
      font-size: 36px;
      line-height: 40px;
    }
  }

  .text-vpn {
    font-size: 24px;
    line-height: 30px;
    margin-top: 36px;
    margin-bottom: 70px;

    @media #{$mobile} {
      font-size: 14px;
      line-height: 20px;
      width: 60%;
      margin: 10px auto 45px;
    }

    @media #{$tablet} {
      font-size: 20px;
      line-height: 20px;
      margin: 10px 0 0;
    }
  }
}

/* Start More Benefits */
.more-benefits {
  background-color: #fff;
  padding-top: 134px;
  padding-bottom: 134px;

  @media #{$maxTab} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .title {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 70px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 40px;
    }
  }

  .more-benefits-content {
    overflow: hidden;

    .item {
      width: 50%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      @media #{$mobile} {
        float: none;
        width: 100%;
        border: 0;
      }

      &:first-child {
        border-right: 1px solid #ebe8df;
        padding-bottom: 64px;
        max-height: 290px;

        @media #{$mobile} {
          border: 0;
          padding-bottom: 40px;
          max-height: auto;
        }
      }

      &:nth-child(2) {
        padding-bottom: 64px;
        max-height: 290px;

        @media #{$mobile} {
          padding-bottom: 40px;
          max-height: auto;
        }
      }

      &:nth-child(5) {
        border-right: 1px solid #ebe8df;
        padding-top: 64px;

        @media #{$mobile} {
          padding-top: 40px;
          border: 0;
        }
      }

      &:nth-child(6) {
        padding-top: 64px;

        @media #{$mobile} {
          padding-top: 40px;
        }
      }

      &:nth-child(4) {
        border-right: 0;
        border-top: 1px solid #ebe8df;
        border-bottom: 1px solid #ebe8df;
        min-height: 370px;

        @media #{$mobile} {
          border: 0;
          min-height: auto;
        }
      }

      &:nth-child(3) {
        border-right: 1px solid #ebe8df;
        border-top: 1px solid #ebe8df;
        border-bottom: 1px solid #ebe8df;
        min-height: 370px;

        @media #{$mobile} {
          border: 0;
          min-height: auto;
          padding-bottom: 40px;
        }
      }

      .icon {
        margin-bottom: 50px;

        @media #{$mobile} {
          transform: scale(0.82);
          margin-bottom: 10px;
        }

        @media #{$betweentabdesk} {
          margin-bottom: 40px;
        }
      }

      .benefit-title {
        margin-bottom: 20px;
        font-size: 20px;
        line-height: 20px;

        @media #{$mobile} {
          font-size: 16px;
          margin-bottom: 12px;
        }

        @media #{$tablet} {
          font-size: 18px;
          margin-bottom: 15px;
        }

        @media #{$betweentabdesk} {
          padding: 0 10px;
        }
      }

      .benefit-text {
        font-size: 16px;
        line-height: 28px;
        width: 74%;
        margin: 0 auto;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 24px;
          width: 85%;
        }

        @media #{$tablet} {
          width: 90%;
          font-size: 15px;
        }

        @media #{$betweentabdesk} {
          width: 90%;
        }
      }
    }
  }
}

/* End More Benefits */

/* Start VPN Protocol */
.vpn-protocol {
  padding: 110px 0 50px;

  @media #{$maxTab} {
    padding: 60px 0;
  }

  .vpn-protocol-img {
    @media #{$tablet} {
      text-align: center;
    }

    img {
      max-width: 100%;
    }
  }

  .vpn-protocol-content {
    @media #{$mobile} {
      padding-left: 16px;
      padding-right: 16px;
    }

    .title {
      margin-top: 80px;
      font-size: 36px;
      line-height: 44px;
      width: 70%;
      margin-bottom: 25px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        margin-top: 0;
      }

      @media #{$tablet} {
        width: 90%;
        margin-top: 0;
      }

      @media #{$betweentabdesk} {
        margin-top: 0;
      }
    }

    p {
      font-size: 14px;
      line-height: 24px;

      @media #{$mobile} {
        font-size: 16px;
        line-height: 28px;
      }

      &.text {
        margin-bottom: 20px;
      }
    }
  }
}

/* End VPN Protocol */

.risk-free.vpn {
  .risk-free-content {
    .risk-free-btn {
      @media #{$mobile} {
        a {
          padding: 24px 48px 23px;
        }
      }
    }
  }
}

/* Start Pricing Tables */
.pricing-tables {
  padding-top: 130px;
  padding-bottom: 130px;

  @media #{$tablet} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .title {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 60px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 40px;
      width: 70%;
      margin: 0 auto 40px;
    }
  }

  .marked {
    background-color: #08ae7e;
    padding: 8px;
    padding-top: 0;
    width: 366px;
    transition: all 0.3s ease-in-out;

    &:hover {
      box-shadow: 0px 6px 9px #e6e4df;
      transform: translateY(-4px);
    }

    @media #{$maxTab} {
      width: auto;
    }

    @media #{$betweentabdesk} {
      width: auto;
    }

    h3 {
      font-size: 20px;
      line-height: 20px;
      font-family: "AvertaSemibold";
      color: #fff;
      padding: 20px 0 20px 12px;
      margin: 0;
      position: relative;

      @media #{$maxTab} {
        font-size: 16px;
        line-height: 20px;
      }

      @media #{$betweentabdesk} {
        font-size: 18px;
        line-height: 20px;
        padding-left: 6px;
      }

      svg {
        position: relative;
        top: -3px;
      }

      span {
        font-size: 16px;
        line-height: 20px;
        background-color: #079e72;
        padding: 12px 19px;
        position: absolute;
        top: 8px;
        right: 0;

        @media #{$betweentabdesk} {
          padding: 12px;
        }
      }
    }
  }

  .price-card {
    background-color: #fff;
    width: 350px;
    transition: all 0.3s ease-in-out;

    @media #{$maxTab} {
      width: auto;
    }

    @media #{$betweentabdesk} {
      width: auto;
    }

    &.one,
    &.three {
      margin-top: 34px;

      &:hover {
        box-shadow: 0px 6px 9px #e6e4df;
        transform: translateY(-4px);
      }

      @media #{$mobile} {
        margin-top: 10px;
      }

      @media #{$tablet} {
        margin-top: 30px;
      }
    }

    &.three {
      position: relative;

      .offer {
        position: absolute;
        top: 23px;
        right: 29px;
        color: $primaryColor;
        font-size: 16px;
        line-height: 20px;
        font-family: "AvertaSemibold";
      }
    }

    input[type="radio"] {
      display: none;

      &:checked+label:before {
        background-color: $primaryColor;
        border-color: $primaryColor;
      }

      &:checked+label:after {
        color: #fff;
      }

      &:checked+.label-radio .price-top p .value,
      &:hover+.label-radio .price-top p .value {
        color: $primaryColor;
      }

      &:checked+.label-radio .price-bottom a {
        color: #fff;
        background-color: $primaryColor;
      }
    }

    .label-radio {
      position: relative;
      cursor: pointer;
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 44px;

      @media #{$mobile} {
        padding: 24px 16px;
      }

      @media #{$tablet} {
        padding: 20px;
      }

      @media #{$betweentabdesk} {
        padding: 24px;
      }

      &:before {
        content: "";
        position: absolute;
        width: 48px;
        height: 48px;
        top: 40px;
        left: 40px;
        border-radius: 50%;
        border: 1px solid #e0ded5;
        text-align: center;
        line-height: 48px;
        transition: all 0.4s ease-in-out;

        @media #{$maxTab} {
          top: 24px;
          left: 16px;
        }
      }

      &:after {
        content: "\f00c";
        position: absolute;
        top: 52px;
        left: 56px;
        color: #e0ded5;
        font-size: 18px;
        font-family: "fontawesome";

        @media #{$maxTab} {
          top: 35px;
          left: 32px;
        }
      }

      .price-top {
        padding-bottom: 45px;
        border-bottom: 1px solid #ebe8df;

        @media #{$maxTab} {
          padding-left: 60px;
          padding-bottom: 20px;
        }

        @media #{$tablet} {
          padding-left: 0;
        }

        .month {
          display: block;
          font-size: 20px;
          line-height: 20px;
          font-family: "AvertaSemibold";
          margin-bottom: 10px;
          margin-top: 65px;

          @media #{$mobile} {
            font-size: 16px;
            margin-top: 0;
            margin-bottom: 5px;
          }

          @media #{$tablet} {
            margin-top: 15px;
          }
        }

        p {
          position: relative;
          display: inline-block;

          @media #{$mobile} {
            margin-left: 35px;
            margin-bottom: 0;
          }

          @media #{$tablet} {
            margin-bottom: 0;
          }

          .value {
            font-size: 64px;
            line-height: 56px;
            font-family: "AvertaBold";
            transition: all 0.3s ease-in-out;

            @media #{$mobile} {
              font-size: 36px;
              line-height: 36px;
            }

            @media #{$tablet} {
              font-size: 50px;
              line-height: 50px;
            }
          }

          .usd {
            position: absolute;
            top: 5px;
            left: -35px;
            font-size: 16px;
            line-height: 20px;
            color: #8d8b92;
            font-family: "AvertaSemibold";

            @media #{$mobile} {
              font-size: 14px;
            }
          }

          .mo {
            position: absolute;
            bottom: 5px;
            right: -35px;
            font-size: 20px;
            line-height: 20px;
            font-family: "Averta";

            @media #{$mobile} {
              font-size: 14px;
            }
          }
        }
      }

      .price-bottom {
        padding-top: 40px;

        @media #{$maxTab} {
          padding-top: 20px;
        }

        .deleted {
          line-height: 20px;
          text-decoration-line: line-through;
          color: #afadb2;
          font-family: "AvertaSemibold";
          margin-right: 3px;
        }

        span {
          font-size: 16px;
          line-height: 20px;
          font-family: "AvertaSemibold";
        }

        p {
          font-size: 16px;
          line-height: 28px;
          color: #8d8b92;
          margin-bottom: 40px;

          @media #{$mobile} {
            margin-bottom: 24px;
          }

          @media #{$tablet} {
            margin-bottom: 30px;
          }
        }

        a {
          display: block;
          width: 100%;
          padding: 24px;

          @media #{$tablet} {
            display: inline-block;
            padding: 24px 48px 23px;
            width: auto;
          }

          @media #{$mobile} {
            padding: 15.5px;
            font-size: 16px;
          }
        }
      }
    }
  }
}

/* End Pricing Tables */