.advantages.benefits-page {
  padding-top: 112px;
  padding-bottom: 134px;

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .benefits-title {
    font-size: 56px;
    line-height: 56px;
    margin-bottom: 35px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      width: 80%;
      margin: 0 auto 10px;
    }

    @media #{$tablet} {
      font-size: 46px;
      line-height: 46px;
      margin-bottom: 20px;
    }
  }

  .benefits-text {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 128px;

    @media #{$mobile} {
      font-size: 14px;
      line-height: 20px;
      width: 60%;
      margin: 0 auto 50px;
    }

    @media #{$tablet} {
      font-size: 22px;
      line-height: 30px;
      margin-bottom: 0;
    }
  }
}

.more-benefits.benefits-page {
  padding-top: 154px;
  padding-bottom: 154px;
}

/* Start Need Help */
.need-help {
  padding-top: 134px;
  padding-bottom: 134px;

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media #{$tablet} {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .need-help-content {
    .need-help-title {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 35px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 24px;
        margin-top: 40px;
      }

      @media #{$tablet} {
        font-size: 30px;
        line-height: 28px;
        margin-bottom: 30px;
      }
    }

    .need-help-list {
      margin-bottom: 30px;

      li {
        font-size: 18px;
        line-height: 32px;
        margin-bottom: 15px;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 14px;
        }

        @media #{$tablet} {
          font-size: 16px;
          line-height: 20px;
        }

        i {
          color: #08ae7e;
          margin-right: 7px;
        }
      }
    }

    .need-help-btn {
      padding: 24px 50px;

      @media #{$mobile} {
        padding: 15.5px 22px;
        font-size: 16px;
      }

      @media #{$tablet} {
        padding: 20px 30px;
        font-size: 18px;
      }
    }

    .help-center {
      font-family: "Averta";
      margin-left: 30px;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 20px;
        margin-left: 15px;
      }
    }
  }

  .need-help-img {
    img {
      max-width: 100%;
    }
  }
}

/* End Need Help */