// header
.wrapper-header.windows {
  padding-bottom: 150px;

  @media #{$maxTab} {
    padding-bottom: 60px;
  }

  @media #{$betweentabdesk} {
    padding-bottom: 80px;
  }
}

.windows-header {
  .navbar {
    .login {
      .login-item {
        .login-link {
          color: #fff;

          @media #{$mobile} {
            color: #5b5863;
          }

          &:hover {
            color: rgba(#fff, 0.7);
          }
        }

        .get-started {
          background-color: #fff;
          color: $primaryColor;

          @media #{$mobile} {
            background-color: $primaryColor;
            color: #fff;
          }

          &:hover {
            background: #fff;
          }
        }
      }
    }
  }
}

.hero-img.windows {
  @media #{$mobile} {
    padding-top: 30px;
    text-align: left;
  }

  .hero-img-content {
    .hero-img-title {
      font-size: 48px;
      line-height: 48px;
      width: 80%;

      @media #{$maxTab} {
        font-size: 36px;
        line-height: 44px;
        width: 100%;
        margin-top: 40px;
        margin-bottom: 12px;
      }

      @media (max-width: 350px) {
        font-size: 32px;
        line-height: 40px;
        width: 100%;
        margin-top: 40px;
        margin-bottom: 12px;
      }

      @media #{$tablet} {
        margin-top: 80px;
      }

      @media #{$betweentabdesk} {
        width: 100%;
        font-size: 42px;
      }
    }

    .hero-img-txt {
      font-size: 24px;
      line-height: 30px;
      margin-top: 32px;
      margin-bottom: 34px;

      @media #{$mobile} {
        font-size: 20px;
        line-height: 20px;
        margin-top: 0;
        margin-bottom: 30px;
      }

      @media #{$tablet} {
        font-size: 22px;
        line-height: 22px;
        margin-top: 0;
        margin-bottom: 30px;
      }

      @media #{$betweentabdesk} {
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }

    .hero-img-list {
      li {
        font-size: 18px;
        line-height: 32px;

        @media #{$mobile} {
          font-size: 16px;
          line-height: 20px;
          margin-bottom: 10px;

          &:last-child {
            margin-bottom: 0;
          }
        }

        i {
          color: #08ae7e;
          margin-right: 7px;
        }
      }
    }

    .hero-img-btn {
      @media #{$betweentabdesk} {
        margin-top: 30px;
      }
    }

    .download-btn {
      padding: 24px 51px;
      color: $primaryColor;
      background-color: transparent;
      margin-left: -4px;
      text-decoration: underline !important;
      display: inline-block;

      @media #{$mobile} {
        text-align: center;
        width: 100%;
        display: block;
        margin-left: 0;
        padding: 0;
      }

      @media #{$tablet} {
        padding: 0;
        text-align: center;
        width: auto;
        margin-left: 30px;
      }

      @media #{$betweentabdesk} {
        padding: 0;
        width: 59%;
        text-align: center;
      }

      &:hover {
        color: #e33866;
        text-decoration: none !important;
      }

      i {
        margin-right: 7px;
        position: static;
      }
    }
  }

  .hero-img-image {
    img {
      top: 120px;
      left: -60px;

      @media #{$maxTab} {
        max-width: 100%;
        position: relative;
        top: 30px;
        left: 0;
      }

      @media #{$mobile} {
        top: 0;
      }

      @media #{$betweentabdesk} {
        max-width: 100%;
        position: relative;
        top: 170px;
        left: 0;
      }
    }
  }

  .header-shape.windows {
    top: -565px;
    right: -825px;
    transform: scale(1.1);

    @media #{$mobile} {
      transform: scale(0.5);
      top: -745px;
      left: -350px;
      right: auto;
    }

    @media #{$tablet} {
      top: -600px;
      right: -730px;
      transform: scale(0.8);
    }

    @media #{$betweentabdesk} {
      top: -620px;
      right: -765px;
      transform: scale(0.9);
    }
  }
}

/* Start Platform */
.platforms {
  padding-top: 40px;

  @media #{$maxTab} {
    padding-top: 0;
  }

  .platform-content {
    p {
      text-align: center;
      line-height: 20px;
      font-family: "AvertaSemibold";
      color: rgba(#5b5863, 0.7);
      margin-bottom: 36px;

      @media #{$mobile} {
        font-size: 12px;
        margin-bottom: 12px;
      }
    }

    .platform-list {
      display: flex;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      align-items: center;

      @media #{$mobile} {
        display: block;
        overflow: hidden;
        border: 1px solid #ebe8df;
      }

      @media #{$tablet} {
        flex-wrap: wrap;
      }

      li {
        font-size: 16px;
        position: relative;
        line-height: 20px;
        font-family: "AvertaSemibold";
        color: rgba(#5b5863, 0.7);
        border-right: 1px solid #ebe8df;
        text-align: center;
        flex: 1;
        height: 136px;
        background-color: #fff;

        @media #{$mobile} {
          float: left;
          width: 50%;
          border: 1px solid #ebe8df;
          height: 44px;
          line-height: 44px;
          text-align: left;
        }

        @media #{$tablet} {
          width: 25%;
          flex: auto;

          &:nth-child(1),
          &:nth-child(2),
          &:nth-child(3),
          &:nth-child(4) {
            border-bottom: 1px solid #ebe8df;
          }
        }

        &.active,
        &:hover {
          a {
            background-color: #f7f6f0;
            position: relative;
            height: 100%;

            span {
              opacity: 1;
            }

            svg {
              path {
                fill: #5b5863;
              }
            }

            .firefox {
              .line {
                stroke: #5b5863;
                fill: none;
              }

              .fox {
                fill: #5b5863;
              }
            }
          }
        }

        a {
          display: block;
          padding: 35px 20px 20px;
          transition: all 0.3s ease-in-out;
          height: 100%;

          @media #{$mobile} {
            padding: 0;
            padding-left: 44px;
            position: relative;
            padding-top: 12px;
            font-size: 12px;
          }

          @media (max-width: 350px) {
            padding: 0;
            padding-left: 32px;
            position: relative;
            padding-top: 12px;
            font-size: 12px;
          }

          &.apple-icon {
            padding-top: 27px;

            @media #{$mobile} {
              padding-top: 12px;
            }
          }
        }

        &:last-child {
          border-right: 0;
        }

        svg {
          @media #{$mobile} {
            width: 16px;
            height: 16px;
            position: absolute;
            left: 20px;
          }

          @media (max-width: 350px) {
            width: 16px;
            height: 16px;
            position: absolute;
            left: 10px;
          }

          path {
            transition: all 0.3s ease-in-out;
          }
        }

        span {
          display: block;
          font-size: 14px;
          line-height: 20px;
          opacity: 0.7;
          color: #5b5863;
          font-family: "Averta";
          margin-top: 8px;
          transition: all 0.3s ease-in-out;

          @media #{$mobile} {
            margin-top: 0;
            font-size: 12px;
          }

          &.line-height {
            line-height: 14px;
          }
        }
      }
    }
  }
}

/* End Platform */

/* Start wevpn-setup */
.wevpn-setup {
  padding-top: 60px;

  @media #{$mobile} {
    padding-top: 30px;
  }

  .wevpn-setup-content {
    padding-bottom: 60px;

    @media #{$tablet} {
      padding-bottom: 80px;
    }

    @media #{$betweentabdesk} {
      padding-bottom: 100px;
    }

    .wevpn-setup-img {
      img {
        max-width: 100%;
      }

      @media #{$mobile} {
        padding-top: 30px;
        padding-bottom: 40px;
      }

      @media #{$tablet} {
        padding-top: 60px;
      }

      @media #{$betweentabdesk} {
        padding-top: 30px;
      }
    }

    .wevpn-setup-info {
      .wevpn-setup-title {
        padding-top: 80px;
        margin-bottom: 20px;
        font-size: 36px;
        line-height: 44px;

        @media #{$mobile} {
          font-size: 24px;
          line-height: 28px;
          padding-top: 0;
        }

        @media #{$tablet} {
          padding-top: 60px;
          font-size: 32px;
        }
      }

      .wevpn-setup-list {
        padding-bottom: 20px;

        li {
          font-size: 18px;
          line-height: 24px;
          padding-left: 30px;
          padding-right: 10px;
          margin-bottom: 15px;
          position: relative;

          @media #{$maxTab} {
            font-size: 16px;
            line-height: 20px;
            margin-bottom: 10px;

            &:last-child {
              margin-bottom: 0;
            }
          }

          span {
            width: 20px;
            height: 20px;
            font-size: 12px;
            font-family: "AvertaSemibold";
            background-color: #08ae7e;
            color: #fff;
            line-height: 20px;
            text-align: center;
            border-radius: 50%;
            display: inline-block;
            position: absolute;
            left: 0;
          }

          a {
            text-decoration: underline;
            color: $primaryColor;
          }
        }
      }

      p {
        border-top: 1px solid #ebe8df;
        font-size: 14px;
        line-height: 18px;
        padding-top: 20px;
        color: #8d8b92;
        margin-left: 35px;

        @media #{$mobile} {
          margin-left: 0;
        }

        @media #{$tablet} {
          font-size: 13px;
        }

        a {
          text-decoration: underline;
          color: $primaryColor;
        }
      }
    }
  }
}

/* End wevpn-setup */

/* Start How to use wevpn */
.how-to-use {
  background-color: #fff;
  padding-top: 100px;
  padding-bottom: 350px;
  text-align: center;

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 170px;

    .container {
      padding-right: 0;
    }
  }

  @media #{$tablet} {
    padding-bottom: 250px;

    .container {
      padding-right: 0;
      margin-right: 0;
    }
  }

  .how-to-use-title {
    font-size: 36px;
    line-height: 44px;
    width: 70%;
    margin: auto;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      padding-right: 20px;
    }

    @media (max-width: 350px) {
      width: 80%;
    }
  }

  .step {
    margin-top: 120px;

    @media #{$mobile} {
      margin-top: 50px;
    }

    .step-text {
      font-size: 20px;
      line-height: 20px;
      position: relative;
      z-index: 1;
      margin-bottom: 75px;

      @media #{$mobile} {
        text-align: left !important;
        font-size: 16px;
        margin-bottom: 28px;
      }

      span {
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 72px;
        line-height: 72px;
        color: #f1efe8;
        font-family: "AvertaBold";

        @media #{$mobile} {
          font-size: 56px;
          line-height: 72px;
          left: 0;
          transform: translateY(-50%);
        }
      }
    }

    .step-img {
      // border-right: 1px solid #eae8e0;
      position: relative;

      @media #{$betweentabdesk} {
        img {
          max-width: 100%;
        }
      }

      &:after {
        content: "";
        position: absolute;
        top: 0;
        right: -15px;
        height: 100%;
        width: 1px;
        background-color: #eae8e0;

        @media #{$maxTab} {
          display: none;
        }

        @media #{$betweentabdesk} {
          display: none;
        }
      }

      &.last {
        &:after {
          display: none;
        }
      }
    }
  }
}

/* End How to use wevpn */

/* Start Video Install */
.video-install {
  .container {
    position: relative;
  }

  .video-install-frame {
    position: absolute;
    background-image: url(../imgs/video-install-bg.png);
    background-position-y: 80%;
    padding: 130px 0;
    top: -250px;
    left: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$mobile} {
      left: 20px;
      right: 20px;
      width: auto;
      background-position: center;
      background-size: cover;
      padding: 56px 0;
      top: -110px;
    }

    @media #{$tablet} {
      left: 0;
      right: 0;
      width: auto;
      padding: 80px 0;
      background-position: center;
      background-size: cover;
      top: -180px;
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #5b5863;
      opacity: 0.9;
      z-index: 1;
    }

    .video-install-content {
      text-align: center;
      position: relative;
      z-index: 5;

      a {
        display: inline-block;
        width: 124px;
        height: 124px;
        line-height: 124px;
        border-radius: 50%;
        background-color: $primaryColor;
        color: #fff;
        text-align: center;
        font-size: 35px;

        @media #{$mobile} {
          width: 72px;
          height: 72px;
          line-height: 72px;
          font-size: 20px;
        }

        @media #{$tablet} {
          width: 90px;
          height: 90px;
          line-height: 90px;
          font-size: 28px;
        }
      }

      p {
        font-size: 24px;
        line-height: 32px;
        color: #ffffff;
        width: 60%;
        margin: 25px auto 0;

        @media #{$mobile} {
          font-size: 16px;
          line-height: 20px;
          margin: 14px auto 0;
        }

        @media (max-width: 350px) {
          width: 70%;
        }
      }
    }
  }
}

/* End Video Install */

