/* Start FAQ */
.faq {
  background-color: #fff;
  padding-top: 134px;

  @media #{$mobile} {
    padding-top: 60px;
  }

  @media #{$tablet} {
    padding-top: 80px;
  }

  @media #{$betweentabdesk} {
    padding-top: 100px;
  }

  .faq-title {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 70px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 20px;
    }

    @media #{$tablet} {
      margin-bottom: 40px;
    }
  }

  .accordion {
    padding: 0 60px;

    @media #{$mobile} {
      padding: 0;
    }

    .question {
      border-bottom: 1px solid #dfddd6;

      &:last-child {
        border: 0;
      }

      .question-header {
        h2 {
          position: relative;
          padding-bottom: 32px;
          padding-top: 32px;
          font-size: 20px;
          line-height: 20px;
          text-decoration: none;
          color: #5b5863;
          cursor: pointer;
          margin-bottom: 0;
          transition: all 0.3s ease-in-out;

          @media #{$mobile} {
            font-size: 13px;
            line-height: 20px;
            padding-bottom: 20px;
            padding-top: 20px;
          }

          @media #{$tablet} {
            font-size: 18px;
            line-height: 20px;
            padding-bottom: 25px;
            padding-top: 25px;
          }

          &.collapsed {
            span {
              opacity: 0.7;
            }

            &:after {
              content: "\f067";
              font-family: fontawesome;
              position: absolute;
              top: 50%;
              right: 0;
              transform: translateY(-50%);
              font-size: 15px;
              color: #878489;

              @media #{$mobile} {
                font-size: 14px;
              }
            }

            img {
              filter: grayscale(100%);
            }
          }

          &:after {
            content: "\f068";
            font-family: fontawesome;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            transition: all 0.3s ease-in-out;
            font-size: 15px;
            color: #5b5863;

            @media #{$mobile} {
              font-size: 14px;
            }
          }

          img {
            filter: none;
            margin-right: 5px;
            transition: all 0.3s ease-in-out;

            @media #{$mobile} {
              transform: scale(0.67);
              margin-right: 0;
              position: relative;
              top: -2px;
            }
          }

          i {
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translateY(-50%);
            font-size: 15px;
          }
        }
      }

      .question-body {
        padding-bottom: 32px;
        padding-top: 32px;
        padding-left: 33px;
        font-size: 14px;
        line-height: 24px;

        @media #{$mobile} {
          border-top: 1px solid #dfddd6;
          font-size: 13px;
          line-height: 24px;
        }

        p:first-child {
          margin-bottom: 20px;
        }
      }
    }
  }

  .faq-btn {
    text-align: center;
    margin-top: 40px;
    padding-bottom: 135px;

    @media #{$mobile} {
      margin-top: 0;
      padding-bottom: 60px;
    }

    @media #{$tablet} {
      margin-top: 20px;
      padding-bottom: 80px;
    }

    @media #{$betweentabdesk} {
      padding-bottom: 100px;
    }

    a {
      background-color: #ffecf1;
      color: $primaryColor;
      padding: 24px 75px 23px;
      display: inline-block;

      @media #{$mobile} {
        display: block;
        padding: 24px 0 23px;
      }

      &:hover {
        background-color: $primaryColor;
        color: #fff;

        i {
          color: #fff;
        }
      }
    }
  }
}

/* End FAQ */

