.hero-img.android {
  .header-shape.android {
    @media #{$betweentabdesk} {
      top: -570px;
    }
  }

  .hero-img-image.android {
    @media #{$mobile} {
      height: 210px;
    }

    @media #{$tablet} {
      height: 470px;
    }

    img {
      position: absolute;
    }

    .one {
      top: 140px;
      left: 310px;

      @media #{$mobile} {
        top: -150px;
        left: 80px;
        transform: scale(0.38);
      }

      @media (max-width: 350px) {
        left: 60px;
      }

      @media #{$tablet} {
        top: 20px;
        left: 310px;
        transform: scale(0.8);
      }

      @media #{$betweentabdesk} {
        top: 100px;
        left: 250px;
        transform: scale(0.8);
      }
    }

    .two {
      top: 70px;
      left: 20px;

      @media #{$mobile} {
        top: -175px;
        left: -30px;
        transform: scale(0.38);
      }

      @media (max-width: 350px) {
        left: -50px;
      }

      @media #{$tablet} {
        top: -30px;
        left: 80px;
        transform: scale(0.8);
      }

      @media #{$betweentabdesk} {
        left: 20px;
        transform: scale(0.8);
        top: 40px;
      }
    }
  }

  .hero-img-btn {
    padding: 24px 64.5px;
    margin-bottom: 10px;

    @media #{$mobile} {
      padding: 24px 0 23px;
    }
  }

  .google-play {
    display: inline-block;
    position: relative;

    @media #{$mobile} {
      display: block;
    }

    .google-play-btn {
      background-color: #2c3038;
      padding: 19.5px 50.5px;
      position: relative;
      top: -1px;
      margin-left: 15px;

      @media #{$mobile} {
        top: 0;
        margin-left: 0;
        display: block;
        text-align: center;
      }

      @media #{$betweentabdesk} {
        top: 0;
        margin-left: 0;
        padding: 20px 79px;
      }

      img:first-child {
        margin-right: 10px;
      }
    }

    .wevpn-apk {
      position: absolute;
      bottom: -23px;
      left: 50%;
      transform: translateX(-50%);
      text-decoration: underline !important;
      color: #8b888d;
      font-size: 13px;
      line-height: 16px;
      display: inline-block;
      cursor: pointer;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: #2c3038;
        text-decoration: none !important;
      }
    }
  }
}

.benefits .benefits-wrapper .benefit.android .benefits-img img {
  right: 115px;
  top: 130px;

  @media #{$mobile} {
    right: -52px;
    top: -160px;
  }

  @media #{$tablet} {
    right: 30px;
    top: 0px;
  }

  @media #{$betweentabdesk} {
    right: 20px;
    top: -40px;
  }
}

.benefits .benefits-wrapper .benefit.android .benfits-img-two {
  border-radius: 55px;
  bottom: 180px;

  @media #{$mobile} {
    bottom: -118px;
    left: -15px;
  }

  @media #{$tablet} {
    left: 145px;
    bottom: 10px;
  }

  @media #{$betweentabdesk} {
    left: 15px;
    bottom: 80px;
  }
}