header.features-page {
  background-color: #fff;
}

.features-windows.features-page {
  padding-top: 112px;
  padding-bottom: 90px;

  @media #{$mobile} {
    padding-top: 50px;
    background-color: #fff;
    padding-bottom: 40px;
  }

  @media #{$tablet} {
    padding-top: 80px;
    padding-bottom: 70px;
  }
}

.features-windows.features-page .features-windows-title {
  font-size: 56px;
  line-height: 56px;
  width: 65%;
  margin-bottom: 136px;

  @media #{$mobile} {
    font-size: 24px;
    line-height: 28px;
    width: 90%;
    margin-bottom: 40px;
  }

  @media #{$tablet} {
    width: 90%;
    font-size: 46px;
    margin-bottom: 80px;
  }
}

.features-windows.features-page .feats .feat.one,
.features-windows.features-page .feats .feat.five {
  margin-top: 60px;

  @media #{$mobile} {
    margin-top: 0;
  }
}

.features-windows.features-page .feats .feat.five {
  @media #{$tablet} {
    margin-top: 30px;
  }
}

.features-windows.features-page .feats .feat.one.repeat,
.features-windows.features-page .feats .feat.five.repeat {
  margin-top: 0;
}

.features-windows.features-page .feats .feat.three.repeat {
  margin-top: -60px;

  @media #{$mobile} {
    margin-top: 0;
  }

  @media #{$tablet} {
    margin-top: -64px;
  }
}