header.border--bottom {
  .container {
    border-bottom: 1px solid #ebe8df;
    padding-bottom: 30px;

    @media #{$mobile} {
      padding-bottom: 0;
      border-bottom: 0;
    }

    @media #{$tablet} {
      padding-bottom: 16px;
    }
  }

  .navbar-show-mobile {
    .container {
      @media #{$maxTab} {
        padding-bottom: 0;
        border-bottom: 0;
      }
    }

    .navbar-toggler {
      background-color: #fff !important;
    }
  }
}

ol {
  list-style-type: decimal-leading-zero;
}

/* Start Terms of Service */
.wrapper-header.terms-of-service {
  @media #{$tablet} {
    padding-bottom: 20px;
  }
}

.terms-of-service,
.privay-policy {
  @media #{$maxTab} {
    padding-bottom: 20px;
  }

  h1 {
    padding-top: 74px;
    margin-bottom: 65px;
    font-size: 36px;
    line-height: 44px;

    @media #{$mobile} {
      padding-top: 25px;
      margin-bottom: 15px;
      font-size: 24px;
    }

    @media #{$tablet} {
      padding-top: 40px;
      margin-bottom: 25px;
      margin-top: 25px;
      font-size: 30px;
    }
  }

  p {
    font-size: 18px;
    line-height: 32px;

    @media #{$mobile} {
      font-size: 16px;
      line-height: 26px;
    }

    &.first {
      margin-bottom: 40px;

      @media #{$mobile} {
        margin-bottom: 20px;
      }

      @media #{$tablet} {
        margin-bottom: 30px;
      }
    }

    strong {
      font-family: "AvertaBold";
    }
  }

  .list-internal {
    padding-left: 90px;
    margin-top: 50px;
    margin-bottom: 50px;

    @media #{$mobile} {
      padding-left: 50px;
      margin-top: 30px;
      margin-bottom: 30px;
    }

    @media #{$tablet} {
      padding-left: 70px;
      margin-top: 40px;
      margin-bottom: 40px;
    }

    li {
      font-size: 18px;
      line-height: 20px;
      transition: color 0.3s ease-in-out;
      font-family: "AvertaSemibold";
      margin-bottom: 20px;

      @media #{$maxTab} {
        font-size: 16px;
        line-height: 26px;
        margin-bottom: 10px;
      }

      a {
        font-size: 18px;
        line-height: 20px;
        text-decoration: underline !important;
        display: block;
        transition: color 0.3s ease-in-out;

        @media #{$maxTab} {
          font-size: 16px;
          line-height: 20px;
        }

        &:hover {
          color: $primaryColor;
        }
      }

      &:hover {
        color: $primaryColor;
      }
    }
  }

  .list-external {
    li {
      font-size: 24px;
      line-height: 36px;
      font-family: "AvertaSemibold";
      margin-bottom: 60px;
      margin-left: 40px;

      @media #{$maxTab} {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 30px;
        margin-left: 30px;
      }

      p {
        margin-top: 24px;
        font-family: "Averta";
        margin-left: -40px;

        @media #{$maxTab} {
          margin-top: 15px;
          margin-left: -20px;
        }
      }
    }
  }
}

/* End Terms of Service */