header.weplay-header {
  .navbar {
    padding-bottom: 32px;
  }
}

.hero-img.weplay-page {
  background-image: url(../imgs/weplay-bg.png);
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-width: 1600px) {
    background-position: 0px -170px;
  }

  @media #{$mobile} {
    background-image: url(../imgs/weplay-bg-m.png);
  }

  @media #{$tablet} {
    background-position: 0px 0;
  }

  @media #{$betweentabdesk} {
    padding: 5px 0 80px;
  }

  @media (min-height: 1000px) {
    height: calc(100vh - 114px);
  }

  .hero-img-content {
    padding-bottom: 390px;

    @media #{$maxTab} {
      padding-bottom: 60px;
    }

    @media #{$betweentabdesk} {
      padding-bottom: 100px;
    }

    .hero-img-title {
      font-size: 47px;
      line-height: 56px;
      margin: 100px 0 40px;
      width: 100%;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 32px;
        text-align: center;
        margin: 20px auto 30px;
        width: 75%;
      }

      @media #{$tablet} {
        font-size: 27px;
        line-height: 36px;
        margin: 50px 0 0;
      }

      @media #{$betweentabdesk} {
        font-size: 36px;
        line-height: 48px;
        margin: 70px 0 20px;
      }
    }

    .hero-img-btn {

      @media #{$betweentabdesk} {
        margin-top: 20px;
      }

      @media #{$tablet} {
        margin-top: 27px;
        font-size: 16px;
        padding: 16px 43px;
      }

    }

    p {
      width: 264px;
      text-align: center;

      @media #{$mobile} {
        width: 100%;
      }

      @media #{$tablet} {
        width: 230px;
      }

      .go-to-dashboard {
        font-size: 20px;
        text-decoration: underline !important;
        color: $primaryColor;
        transition: all 0.3s ease-in-out;

        @media #{$tablet} {
          width: 230px;
          font-size: 16px;
        }

        i {
          margin-left: 8px;
          transition: all 0.3s ease-in-out;
        }

        &:hover {
          color: $linkHoverColor;
          text-decoration: none !important;

          i {
            transform: translateX(4px);
          }
        }
      }
    }

    .arrow-down {
      width: 64px;
      height: 64px;
      background-color: $primaryColor;
      position: absolute;
      bottom: 34px;
      left: 0;
      border-radius: 50%;
      display: none;
      transition: all 0.3s ease-in-out;

      @media (min-width: 1600px) {
        display: block;
      }

      &:hover {
        background-color: $linkHoverColor;
      }

      .arrow {
        opacity: 0;
        position: absolute;
        left: 46%;
        top: 65%;
        transform-origin: 50% 50%;
        transform: translate3d(-50%, -50%, 0);
      }

      .arrow-first {
        animation: arrow-movement $ani-speed ease-in-out infinite;
      }

      .arrow-second {
        animation: arrow-movement $ani-speed $ani-delay ease-in-out infinite;
      }

      .arrow:before,
      .arrow:after {
        background: #fff;
        content: '';
        display: block;
        height: 2px;
        position: absolute;
        top: 0;
        left: 0;
        width: 15px;
      }

      .arrow:before {
        transform: rotate(45deg) translateX(-23%);
        transform-origin: top left;
      }

      .arrow:after {
        transform: rotate(-45deg) translateX(23%);
        transform-origin: top right;
      }

      // Animation
      @keyframes arrow-movement {
        0% {
          opacity: 0;
          top: 45%;
        }

        70% {
          opacity: 1;
        }

        100% {
          opacity: 0;
        }
      }

    }
  }

  .hero-img-image {
    img {
      left: auto;
      right: 0;
      top: 120px;

      @media #{$tablet} {
        top: 40px;
      }

      @media #{$betweentabdesk} {
        top: 50px;
      }
    }

    @media #{$mobile} {
      position: static;
      padding-top: 30px;
    }


  }
}

/* start weplay info */
.weplay-info {
  padding: 140px 0;

  @media #{$mobile} {
    padding: 60px 0;
    overflow: hidden;
  }

  @media #{$tablet} {
    padding: 60px 0 100px;
  }

  @media #{$betweentabdesk} {
    padding: 80px 0;
  }

  .weplay-info-txt {
    h3 {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 28px;
      margin-top: 30px;
      width: 65%;

      @media #{$mobile} {
        line-height: 28px;
        margin-bottom: 25px;
        margin-top: 0;
        text-align: center;
        margin: 0 auto 25px;
        width: 85%;
      }

      @media #{$tablet} {
        width: 100%;
      }

      @media #{$betweentabdesk} {
        width: 90%;
        margin-top: 15px;
      }
    }

    p {
      line-height: 32px;
      width: 83%;

      @media #{$mobile} {
        line-height: 28px;
        text-align: center;
        width: 97%;
        margin: 0 auto 36px;
      }

      @media #{$tablet} {
        width: 100%;
        margin-bottom: 35px;
        line-height: 28px;
      }

      @media #{$betweentabdesk} {
        width: 90%;
      }
    }

    span {
      color: $primaryColor;
    }
  }

  .weplay-video {
    position: relative;
    background-image: url(../imgs/video-install-bg.png);
    background-position-y: 64%;
    background-position-x: 64%;
    box-shadow: 0px 34px 86px rgba(107, 104, 114, 0.63);
    padding: 80px 0 100px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$mobile} {
      background-position: center;
      background-size: cover;
      padding: 47px 0;
    }

    @media #{$tablet} {
      padding: 80px 0;
      background-position: center;
      background-size: cover;
    }

    &:after {
      content: "";
      @include overlay;
      background-color: #5b5863;
      opacity: 0.9;
      z-index: 1;
    }

    .weplay-video-content {
      text-align: center;
      position: relative;
      z-index: 5;

      a {
        display: inline-block;
        width: 102px;
        height: 102px;
        line-height: 102px;
        border-radius: 50%;
        background-color: $primaryColor;
        color: #fff;
        text-align: center;
        font-size: 35px;

        @media #{$mobile} {
          width: 60px;
          height: 60px;
          line-height: 60px;
          font-size: 24px;
        }

        @media #{$tablet} {
          width: 90px;
          height: 90px;
          line-height: 90px;
          font-size: 28px;
        }

        @media #{$betweentabdesk} {
          width: 78px;
          height: 78px;
          line-height: 78px;
          font-size: 28px;
        }
      }

      p {
        font-size: 24px;
        line-height: 24px;
        color: #fff;
        margin: 25px auto 0;

        @media #{$mobile} {
          font-size: 14px;
          margin: 8px auto 0;
        }

        @media #{$betweentabdesk} {
          font-size: 20px;
          margin: 20px auto 0;
        }
      }
    }
  }
}

/* End weplay info */

/* Start how work weplay */
.how-work-weplay {
  background-color: #fff;
  padding: 50px 0 90px;

  @media #{$mobile} {
    padding: 60px 0;
  }

  .title {
    font-size: 96px;
    line-height: 120px;

    @media #{$mobile} {
      font-size: 36px;
      line-height: 44px;
    }

    @media #{$tablet} {
      font-size: 54px;
      line-height: 65px;
    }

    @media #{$betweentabdesk} {
      font-size: 70px;
      line-height: 70px;
    }
  }

  .desc {
    font-size: 18px;
    line-height: 28px;
    width: 65%;
    margin: 35px auto 0;

    @media #{$mobile} {
      font-size: 16px;
      width: 99%;
      margin: 20px auto 0;
    }

    @media #{$tablet} {
      width: 85%;
      margin: 20px auto 0;
    }

    strong {
      font-family: "AvertaSemibold"
    }
  }

  .section-line {
    border-color: #E0DFDC;
    margin: 65px 0;

    @media #{$mobile} {
      margin: 60px 0;
    }
  }

  .step {

    &.one,
    &.two {
      position: relative;

      @media #{$maxTab} {
        padding-bottom: 100px;
      }

      &:after {
        content: "";
        border-width: 19px;
        border-style: solid;
        border-color: transparent transparent transparent rgba(#AFADB2, 0.4);
        position: absolute;
        display: block;
        top: 93px;
        right: 30px;

        @media #{$maxTab} {
          top: auto;
          bottom: 25px;
          right: auto;
          left: 50%;
          transform: translateX(-50%);
          border-color: rgba(#AFADB2, 0.4) transparent transparent transparent;
        }
      }
    }

    .icon {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: $primaryColor;
      position: relative;
      overflow: hidden;

      @media #{$mobile} {
        width: 40px;
        height: 40px;
        position: absolute;
      }

      @media #{$tablet} {
        position: absolute;
      }

      span {
        color: #fff;
        font-size: 96px;
        line-height: 120px;
        font-family: "AvertaBold";
        position: absolute;
        top: -18px;
        left: 10px;

        @media #{$mobile} {
          font-size: 60px;
          line-height: 60px;
          top: -4px;
          left: 7px;
        }
      }
    }

    .step-info {
      @media #{$mobile} {
        padding-left: 56px;
      }

      @media #{$tablet} {
        padding-left: 80px;
      }

      h3 {
        font-size: 20px;
        line-height: 20px;
        margin: 45px 0 15px;

        @media #{$mobile} {
          margin: 0 0 15px;
          font-size: 16px;
        }

        @media #{$tablet} {
          margin: 0;
        }
      }

      p {
        line-height: 28px;
        width: 69%;

        @media #{$mobile} {
          width: 100%;
          font-size: 14px;
        }
      }
    }
  }
}

/* End how work weplay */

/* Start Why Weplay */
.why-weplay {
  padding: 100px 0 160px;
  position: relative;

  .container {
    position: relative;
  }

  @media #{$mobile} {
    padding: 60px 0 140px;
  }

  @media #{$tablet} {
    padding: 80px 0 85px;
  }

  @media #{$betweentabdesk} {
    padding: 100px 0 140px;
  }

  .title {
    font-size: 56px;
    line-height: 56px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 46px;
    }

    @media #{$tablet} {
      font-size: 36px;
      line-height: 56px;
    }
  }

  .txt {
    font-size: 18px;
    line-height: 28px;
    margin: 32px 0 60px;

    @media #{$maxTab} {
      margin: 0;
    }
  }

  .why-weplay-content {
    @media #{$mobile} {
      padding: 0 21px;
    }

    h3 {
      font-size: 20px;
      line-height: 20px;

      @media #{$mobile} {
        font-size: 16px;
      }
    }

    p {
      line-height: 28px;
      margin: 15px 0 35px;
      width: 86%;

      @media #{$mobile} {
        width: 100%;
        font-size: 14px;
      }
    }
  }

  .why-weplay-img {
    text-align: center;

    @media (max-width: 1200px) {
      img {
        max-width: 100%;
      }
    }
  }

  /* Start join-to-weplay */
  .join-to-weplay {

    position: absolute;
    bottom: -285px;
    left: 15px;
    right: 15px;

    @media #{$tablet} {
      bottom: -165px;
    }

    @media #{$betweentabdesk} {
      bottom: -240px;
    }

    .join-to-weplay-content {
      background-color: $primaryColor;
      padding: 87px 80px;

      @media #{$mobile} {
        padding: 54px 32px;
      }

      @media #{$tablet} {
        padding: 50px 30px;
      }

      @media #{$betweentabdesk} {
        padding: 70px 50px;
      }

      .row {
        align-items: center;
      }

      .text {
        p {
          font-size: 24px;
          line-height: 32px;
          color: #F9F8F4;
          font-family: "AvertaSemibold";

          @media #{$mobile} {
            font-size: 18px;
            line-height: 28px;
            text-align: center;
            width: 95%;
            margin: 0 auto 30px;
          }

          @media #{$tablet} {
            font-size: 18px;
            line-height: 28px;
          }

          @media #{$betweentabdesk} {
            font-size: 20px;
            line-height: 28px;
          }
        }
      }

      .btns {
        text-align: center;

        .start-btn {
          background-color: #fff;
          color: $primaryColor;
          padding: 21px 48px;
          margin-right: 16px;

          @media #{$maxTab} {
            padding: 13px 0;
            width: 100%;
            margin-right: 0;
          }

          @media #{$betweentabdesk} {
            padding: 21px 25px;
            margin-right: 10px;
          }
        }

        .login-btn {
          color: #fff;
          background-color: $linkHoverColor;
          padding: 21px 43px;

          @media #{$betweentabdesk} {
            padding: 21px 25px;
          }
        }
      }
    }
  }

  /* End join-to-weplay */

}

.weplay-page-footer {
  padding-top: 223px;

  @media #{$mobile} {
    padding-top: 203px;
  }

  @media #{$tablet} {
    padding-top: 160px;
  }

  @media #{$betweentabdesk} {
    padding-top: 200px;
  }
}

/* End Why Weplay */