@font-face {
  font-family: "AvertaBold";
  src: url(../fonts/Averta-Bold.otf);
}

@font-face {
  font-family: "Averta";
  src: url(../fonts/Averta.otf);
}

@font-face {
  font-family: "AvertaSemibold";
  src: url(../fonts/Averta-Semibold.otf);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input,
textarea {
  color: #5b5863 !important;
  box-shadow: none !important;
  outline: none !important;
  height: auto;

  ::placeholder {
    color: #8d8b92;
  }

  &:focus {
    &::-webkit-input-placeholder {
      opacity: 0.5;
      transition: opacity 0.3s ease-in-out;
      box-shadow: none;
      outline: none;
    }
  }
}

.wevpn-link {
  color: $primaryColor;
  text-decoration: underline !important;
  transition: all 0.3s ease-in-out;
  font-family: "AvertaSemibold";

  i {
    font-weight: bold;
    margin-left: 5px;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    color: $linkHoverColor;
    text-decoration: none !important;

    i {
      transform: translateX(3px);
    }
  }
}

.main-bg {
  background-color: #f9f8f4;
}

select {
  background: url(../imgs/Arrow.png) no-repeat right;
  -webkit-appearance: none;

  &:invalid {
    color: #8d8b92;
  }

  & option:first-child {
    color: #8d8b92;
  }

  &:invalid option:not(:first-child) {
    color: #5b5863;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.bull {
  color: #c1c0c4;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Firefox */
}

body {
  font-family: "Averta", sans-serif;
  position: relative;
  overflow-x: hidden;
  color: #5b5863;
  background-color: #f9f8f4;

  &.body-dashboard {
    background-color: #f2f2ed;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "AvertaBold", sans-serif;
  margin-bottom: 0;
}

p {
  margin-bottom: 0;
}

::selection {
  background-color: #5b5863;
  color: #fff;
}

a {
  color: #5b5863;
  text-decoration: none !important;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    position: relative;
  }
}

@media #{$mobile} {
  .container {
    padding: 0 20px;
    position: relative;
  }
}

.wrapper {
  overflow: hidden;
}

.wrapper-header {
  overflow: hidden;
  padding-bottom: 75px;

  @media #{$mobile} {
    padding-bottom: 0;
  }
}

.heading-medium {
  font-size: 36px;
  line-height: 44px;

  @media #{$mobile} {
    font-size: 24px;
    line-height: 28px;
  }
}

button:focus {
  box-shadow: none;
  outline: none;
}

body.overlay:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#000, 0.5);
  z-index: 1;
  margin: 0;
  overflow: hidden;
}

.arrow {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 9px;
  height: 15px;
  display: inline-block;

  span {
    top: 0.5rem;
    position: absolute;
    width: 6px;
    height: 1.5px;
    background-color: #acaaae;
    display: inline-block;
    transition: all 0.4s ease;

    &:first-of-type {
      left: 0;
      transform: rotate(45deg);
    }

    &:last-of-type {
      right: 0;
      transform: rotate(-45deg);
    }
  }
}