/* Start Header */
header {
  position: relative;
  z-index: 3;

  @media #{$maxTab} {
    height: 76px;
  }

  .navbar-show-mobile {
    background-color: #fff !important;
  }

  .navbar {
    padding-top: 32px;

    @media #{$maxTab} {
      padding: 20px 25px;
      position: absolute !important;
      z-index: 9999;
      width: 100%;

      .navbar-collapse {
        border-top: 1px solid #ebe8df;
        margin: 20px -25px 0;
      }
    }

    .navbar-brand {
      margin-right: 33px;

      @media #{$maxTab} {
        padding: 0;
        margin: 0;

        img {
          transform: scale(0.9);
        }
      }
    }

    .navbar-nav {
      @media #{$maxTab} {
        padding-left: 10px;
        padding-top: 25px;

        .nav-item {
          font-size: 14px;
          line-height: 40px;
        }
      }

      .nav-link {
        padding-left: 14px;
        padding-right: 14px;

        @media #{$maxTab} {
          line-height: 25px;
        }

        @media #{$betweentabdesk} {
          padding-left: 10px;
          padding-right: 10px;
        }
      }
    }

    .login {
      @media #{$maxTab} {
        padding-left: 10px;
        padding-right: 20px;
      }

      .login-item {
        display: inline-block;

        &:first-child {
          margin-right: 12px;
        }

        &:last-child {
          @media #{$maxTab} {
            display: block;
            border-top: 1px solid #ebe8df;
            margin-top: 20px;
            padding-top: 25px;
            margin-left: -25px;
            margin-right: -25px;
          }
        }

        .login-link {
          font-size: 16px;
          line-height: 19px;
          color: inherit;
          padding: 8px 12px;
          display: block;
          transition: color 0.3s ease-in-out;
          font-family: "AvertaSemibold";
          text-decoration: none;

          @media #{$maxTab} {
            color: #5b5863;
          }

          &:hover {
            color: $primaryColor;
          }
        }

        .get-started {
          padding: 10px 20px;
          display: block;
          font-size: 14px;
          text-align: center;
          transition: background-color 0.3s ease-in-out;
          background-color: $primaryColor;
          text-decoration: none;
          color: #fff;

          @media #{$maxTab} {
            margin-left: 35px;
            margin-right: 25px;
            padding: 13.5px;
            color: #fff;
            background-color: $primaryColor;
          }

          &:hover {
            background-color: $linkHoverColor;
          }
        }
      }
    }
  }
}

.navbar-light .navbar-toggler {
  padding: 4px 11px 8px;
  border-radius: 0;
  border: 1px solid #e1e0de;
  width: 36px;
  height: 36px;

  img {
    &:last-child {
      display: none;
    }
  }
}

.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show>.nav-link {
  color: $primaryColor;
}

.navbar-light .navbar-nav .nav-link {
  font-family: "AvertaSemibold";
  color: #5b5863;
  font-size: 16px;
  line-height: 19px;
  transition: color 0.3s ease-in-out;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: $primaryColor;
}

.dropdown-toggle {
  &::after {
    content: "\f107";
    font-family: fontawesome;
    border: 0;
    color: #928e9d;
    vertical-align: middle;
    margin-left: 5px;
    transition: color 0.3s ease-in-out;
  }

  &:hover {
    &:after {
      color: $primaryColor;
    }
  }
}

/* End Header */