header.dashboard-page {
  background-color: #fff;

  @media #{$maxTab} {
    height: 76px;
  }

  .navbar {
    padding-bottom: 30px;
  }

  .get-started-login {
    @media #{$maxTab} {
      padding-left: 25px;
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  p {
    font-size: 14px;
    line-height: 17px;
    color: #8d8b92;
    margin-bottom: 0;

    a {
      color: $primaryColor;
      margin-left: 5px;
      text-decoration: underline !important;
      font-family: "AvertaSemibold";
      font-size: 16px;

      i {
        font-weight: bold;
        transition: all 0.3s ease-in-out;
      }

      &:hover {
        text-decoration: none !important;
        color: $linkHoverColor;

        i {
          transform: translateX(2px);
        }
      }
    }
  }
}

/* Start Dashboard */
.dashboard {
  background-color: $primaryColor;
  color: #fff;
  padding-top: 55px;

  @media #{$mobile} {
    padding-top: 24px;

    .container {
      padding: 0;
    }
  }

  @media #{$tablet} {
    .container {
      padding: 0;
      max-width: 100%;
    }
  }

  h2 {
    font-size: 56px;
    line-height: 56px;
    padding-bottom: 60px;
    margin-bottom: 0;

    @media #{$mobile} {
      font-size: 28px;
      line-height: 32px;
      padding-bottom: 28px;
    }

    @media #{$tablet} {
      font-size: 46px;
      line-height: 46px;
      padding-bottom: 45px;
    }
  }

  .dashboard-list {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgba(#f9f8f4, 0.2);

    @media #{$maxTab} {
      overflow-x: auto;
      display: -webkit-box;
    }

    li {
      opacity: 0.7;
      transition: all 0.3s ease-in-out;

      &.active,
      &:hover {
        opacity: 1;

        a:before {
          opacity: 1;
          visibility: visible;
          height: 3px;
        }
      }

      &.active {
        svg {
          .active-item {
            visibility: visible;
            opacity: 1;
          }

          .inactive-item {
            visibility: hidden;
            opacity: 0;
          }
        }
      }

      a {
        padding: 25px 45px;
        color: #f9f8f4;
        font-family: "AvertaSemibold";
        display: block;
        position: relative;

        @media #{$mobile} {
          padding: 15px 20px 12px;
          font-family: "Averta";
        }

        @media #{$tablet} {
          padding: 17px 40px;
          font-family: "Averta";
          font-size: 16px;
        }

        @media #{$betweentabdesk} {
          padding: 20px 23px;
          font-family: "Averta";
          font-size: 16px;
        }

        &:hover {
          &:before {
            opacity: 1;
            visibility: visible;
            height: 3px;
          }
        }

        &:before {
          content: "";
          display: block;
          width: 100%;
          height: 0;
          position: absolute;
          top: 0;
          left: 0;
          background-color: #fff;
          visibility: hidden;
          opacity: 0;
          transition: all 0.3s ease-in-out;
        }

        svg {
          margin-right: 12px;
          position: relative;
          top: -2px;

          @media #{$mobile} {
            margin-right: 4px;
          }

          @media #{$tablet} {
            margin-right: 0;
          }

          .active-item {
            visibility: hidden;
            opacity: 0;
            transition: all 0.3s ease-in-out;
          }

          .inactive-item {
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in-out;
          }
        }

        .new {
          background-color: #08ae7e;
          border-radius: 3px;
          text-transform: uppercase;
          color: #fff;
          padding: 2px 4px;
          font-size: 12px;
          line-height: 12px;
        }
      }
    }
  }
}

/* End Dashboard */

/* Start Download */
.download {
  margin-top: 60px;
  margin-bottom: 40px;

  @media #{$mobile} {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .download-content {
    background: #ffffff;
    border: 1px solid #ebe8df;
    overflow: hidden;

    .item {
      width: 50%;
      float: left;
      padding: 34px 60px;
      height: 132px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media #{$mobile} {
        width: 100%;
        float: none;
        display: block;
        padding: 20px 24px 24px;
        height: auto;
      }

      @media #{$tablet} {
        display: block;
        padding: 20px 24px 24px;
      }

      @media #{$betweentabdesk} {
        height: 120px;
        padding: 30px;
      }

      &:first-child {
        border-right: 1px solid #ebe8df;

        @media #{$mobile} {
          border-bottom: 1px solid #ebe8df;
          border-right: 0;
        }
      }

      p {
        display: inline-block;
        margin-bottom: 0;
        width: 120px;
        line-height: 20px;
        opacity: 0.7;

        @media #{$maxTab} {
          width: 100%;
          display: block;
          text-align: center;
          margin-bottom: 5px;
          font-size: 12px;
        }

        @media #{$tablet} {
          font-size: 15px;
          margin-bottom: 10px;
        }
      }

      a {
        color: #ffffff;
        font-family: "AvertaSemibold";
        display: inline-block;
        background-color: $primaryColor;
        padding: 20px 46px;
        height: 64px;

        @media #{$maxTab} {
          display: block;
          padding: 14px 42px;
          font-family: "Averta";
          width: 100%;
          height: 52px;
        }

        @media #{$mobile} {
          padding: 14px 0;
          text-align: center;
        }

        @media #{$betweentabdesk} {
          padding: 20px 30px;
        }

        svg {
          margin-right: 5px;
          position: relative;
          top: -2px;
        }

        &.web-store {
          border: 1px solid #e3e0d3;
          background: none;
          color: #5b5863;

          @media #{$mobile} {
            padding: 12px 42px;
          }

          img {
            position: relative;
            top: -2px;
            left: -5px;
          }
        }
      }
    }
  }
}

/* End Download */

/* Start Subscribe */
.subscribe {
  padding-bottom: 60px;

  @media #{$mobile} {
    padding-bottom: 20px;
  }

  .padd-right {
    padding-right: 20px;

    @media #{$mobile} {
      padding-right: 15px;
    }
  }

  .padd-left {
    padding-left: 20px;

    @media #{$mobile} {
      padding-left: 15px;
    }
  }

  .left-column,
  .right-column {
    background-color: #fff;

    .item-heading {
      h3 {
        font-size: 24px;
        line-height: 35px;
        position: relative;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        padding-left: 40px;
        padding-right: 50px;
        padding-top: 22px;
        padding-bottom: 22px;
        margin-bottom: 0;

        @media #{$mobile} {
          font-size: 18px;
          line-height: 18px;
          padding: 19px 13px;
        }

        @media #{$tablet} {
          font-size: 20px;
          line-height: 30px;
          padding: 20px;
        }

        .settings {
          font-size: 12px;
          line-height: 24px;
          text-align: center;
          color: #8d8b92;
          font-family: "Averta";
          border: 1px solid #e3e0d2;
          border-radius: 5px;
          display: inline-block;
          padding: 5px 12px;
          position: absolute;
          right: 50px;
          top: 22px;
          background: none;

          @media #{$mobile} {
            right: 13px;
            top: 12px;
            padding: 3px 7px;

            &:not(svg) {
              font-size: 0;
            }
          }

          @media #{$tablet} {
            right: 20px;
            top: 17px;
          }
        }
      }
    }

    .list {
      padding-left: 40px;
      padding-right: 50px;

      @media #{$mobile} {
        padding: 0 13px;
      }

      @media #{$tablet} {
        padding: 0 20px;
      }

      li {
        padding-top: 35px;
        padding-bottom: 35px;
        position: relative;

        @media #{$mobile} {
          padding: 12px 0;
        }

        @media #{$tablet} {
          padding-bottom: 29px;
        }

        &:not(:last-child) {
          border-bottom: 1px dashed rgba(#e3e0d3, 0.7);
        }

        .list-item-text {
          label {
            font-size: 14px;
            line-height: 20px;
            color: #8d8b92;
            font-family: "Averta";
            margin-bottom: 2px;

            @media #{$mobile} {
              font-size: 12px;
            }
          }

          p {
            font-size: 16px;
            line-height: 20px;
            color: #5a5761;
            font-family: "AvertaSemibold";
            margin-bottom: 5px;

            @media #{$mobile} {
              font-size: 14px;
            }

            @media #{$tablet} {
              width: 50%;
            }
          }
        }

        .button {
          position: absolute;
          top: 40px;
          right: 0;
          font-size: 14px;
          line-height: 40px;
          padding: 0px 16px;
          display: inline-block;

          @media #{$mobile} {
            top: 12px;
          }

          &:hover {
            color: #fff;
          }

          &.edit {
            background: #ffecf1;
            color: $primaryColor;
            font-family: "AvertaSemibold";
            padding: 0px 16px;
            border: 0;
          }
        }

        .link {
          font-size: 14px;
          line-height: 20px;
          text-decoration: underline !important;
          color: $primaryColor;
          margin-top: 15px;
          margin-bottom: 16px;
          display: inline-block;

          @media #{$mobile} {
            font-size: 12px;
            margin-top: 5px;
          }

          @media #{$tablet} {
            margin-bottom: 14px;
          }
        }
      }
    }
  }

  .right-column {
    @media #{$mobile} {
      margin-top: 12px;
    }

    .list {
      li {
        position: relative;
        padding-left: 50px;
        padding-top: 12px;
        padding-bottom: 12px;
        overflow: hidden;

        &:not(:last-child) {
          border-bottom: 1px dashed rgba(#e3e0d3, 0.7);
        }

        p {
          margin-bottom: 0;
          font-size: 16px;
          line-height: 20px;
          color: #5a5761;
          font-family: "AvertaSemibold";
        }

        span {
          font-size: 14px;
          line-height: 20px;
          color: #8d8b92;
        }

        .icon {
          position: absolute;
          top: 13px;
          left: 0;
          filter: grayscale(100%);
          opacity: 0.8;
        }

        .download {
          position: absolute;
          top: -40px;
          right: 0;

          @media #{$mobile} {
            top: 0;
          }
        }
      }
    }
  }
}

.modal-dialog {
  max-width: 840px;
  margin-top: 174px;

  @media #{$mobile} {
    width: auto;
    margin: 20px;
  }

  @media #{$tablet} {
    max-width: 700px;
  }

  .padd {
    padding-right: 10px;
    padding-left: 10px;

    @media #{$mobile} {
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  .padd-right {
    padding-right: 10px;

    @media #{$mobile} {
      padding-right: 15px;
    }
  }

  .padd-left {
    padding-left: 10px;

    @media #{$mobile} {
      padding-left: 15px;
    }
  }

  .modal-content {
    padding: 60px 25px 45px;
    border-radius: 0;

    @media #{$mobile} {
      padding: 24px 10px;
    }

    .creditModal-header {
      position: relative;

      h5 {
        font-size: 24px;
        line-height: 24px;
        margin-bottom: 45px;

        @media #{$mobile} {
          margin-bottom: 24px;
          text-align: left !important;
          padding-left: 15px;
        }
      }

      .close {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 1px solid #ccc;
        line-height: 12px;
        padding: 0 !important;
        opacity: 1;
        position: absolute;
        top: -10px;
        right: 15px;

        @media #{$mobile} {
          width: 40px;
          height: 40px;
        }
      }
    }
  }

  .modal-body {
    @media #{$mobile} {
      padding: 0 14px;
    }

    @media #{$tablet} {
      padding: 0;
    }

    .form-group {
      border: 1px solid #e0ded5;
      padding: 16px 30px;
      margin-bottom: 20px;
      padding: 12px 30px 18px;

      @media #{$mobile} {
        margin-bottom: 8px;
        padding: 7px 16px;
      }

      @media #{$tablet} {
        padding: 12px 20px 15px;
      }

      label {
        font-size: 12px;
        line-height: 20px;
        font-family: "AvertaSemibold";
        margin-bottom: 0;

        @media #{$mobile} {
          font-family: "Averta";
        }

        svg {
          margin-left: 5px;
        }
      }

      input {
        border: 0;
        padding: 0;
        font-family: "AvertaSemibold";

        @media #{$mobile} {
          line-height: 20px;
          font-size: 14px;
        }

        &::placeholder {
          font-family: "Averta";
        }
      }
    }

    .save {
      padding: 13px 42px;
      float: right;
      margin-top: 20px;
      font-size: 20px;
      color: #fcf9f2;

      @media #{$mobile} {
        line-height: 20px;
        font-size: 16px;
        margin-top: 0;
        display: block;
        width: 100%;
        padding: 18px 0;
      }
    }
  }
}

/* End Subscribe */
.modal-backdrop.show {
  opacity: 0.7;
}

.modal-backdrop {
  background-color: #5b5863;
}

.contacting.dashboard-page {
  background-color: #fff;
  padding-bottom: 0;

  @media #{$mobile} {
    padding-top: 20px;
  }

  .contacting-content {
    padding-bottom: 60px;

    @media #{$mobile} {
      padding-bottom: 37px;
    }
  }
}

.popular-tickets.dashboard-page {
  background-color: #fff;
  border-bottom: 1px solid #ebe8df;

  .popular-tickets-list {
    margin-bottom: 50px;
  }
}

.list-content {
  >div {
    &:not(:first-child) {
      display: none;
    }
  }

  .content-two {
    @media #{$mobile} {
      background-color: #f1f1ed;
      padding-top: 20px;
      padding-bottom: 60px;
    }

    table {
      margin-top: 60px;
      margin-bottom: 195px;
      background-color: #fff;

      @media #{$tablet} {
        margin-top: 50px;
        margin-bottom: 100px;
      }

      td {
        border-bottom: 1px solid #e0ded5;
        border-top: 0;
        font-size: 14px;
        color: #5b5863;
        height: 64px;
        padding: 0;
        line-height: 64px;
      }

      thead {
        tr {
          background-color: #f5f4f0;
          border-top: 1px solid #e0ded5;

          th {
            font-size: 12px;
            text-transform: uppercase;
            color: #8d8b92;
            border-bottom: 0;
            height: 56px;
            line-height: 56px;
            padding-top: 0;
            padding-bottom: 0;
            padding-left: 0;

            &:first-child {
              padding-left: 32px;
            }
          }
        }
      }

      tbody {
        td[scope="row"] {
          font-size: 14px;
          color: #8d8b92;
          padding-left: 32px;
        }

        td:nth-child(2) {
          color: $primaryColor;
        }
      }
    }

    .invoices {
      >.cell {
        background-color: #fff;
        position: relative;
        padding: 0 28px;
        border-bottom: 1px solid #e0ded5;
        height: 64px;
        line-height: 64px;

        >span {
          font-size: 16px;
          line-height: 20px;
          font-family: "AvertaSemibold";
          color: $primaryColor;
        }

        &.active {
          .arrow {
            span {
              background-color: #5b5863;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
      }

      .toggle {
        display: none;

        .cell {
          padding: 0 28px;
          height: 54px;
          line-height: 54px;
          border-bottom: 1px solid #ebeae5;
          background-color: #fbfaf9;

          &:last-child {
            border-bottom: 1px solid #e0ded5;
          }

          p {
            margin-bottom: 0;
            font-size: 14px;
            color: #8d8b92;

            span {
              float: right;

              &.payment {
                color: #5a5761;
              }

              &.amount,
              &.subscription {
                font-size: 14px;
                color: #5b5863;
              }
            }
          }
        }
      }
    }
  }

  .content-three {
    @media #{$mobile} {
      background: rgba(#f1f1ed, 0.3);
    }

    .subscription-settings {
      margin-top: 80px;
      margin-bottom: 200px;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;

      @media #{$mobile} {
        margin-top: 20px;
        margin-bottom: 40px;
      }

      @media #{$tablet} {
        margin-top: 50px;
        margin-bottom: 100px;
      }

      .subscription-content {
        background-color: #fff;
        padding: 50px 60px 0;

        @media #{$mobile} {
          padding: 19px 13px;
        }

        @media #{$tablet} {
          padding: 40px 40px 0;
        }
      }

      h3 {
        font-size: 24px;
        line-height: 44px;
        color: #8d8b92;
        font-family: "Averta";
        border-bottom: 1px solid #e3e0d3;
        margin-bottom: 0;
        padding-bottom: 15px;

        @media #{$mobile} {
          font-size: 18px;
          line-height: 18px;
          margin-bottom: 0;
          padding-bottom: 19px;
        }
      }

      .list {
        margin-top: 10px;

        @media #{$mobile} {
          margin-top: 0;
        }

        li {
          padding-top: 25px;
          padding-bottom: 25px;
          position: relative;

          @media #{$mobile} {
            padding: 20px 0;
          }

          &:not(:last-child) {
            border-bottom: 1px dashed #e3e0d3;
          }

          .list-item-text {
            p {
              font-size: 16px;
              line-height: 20px;
              color: #5a5761;
              font-family: "AvertaSemibold";
              margin-bottom: 5px;

              @media #{$mobile} {
                font-size: 14px;
                margin-bottom: 0;
              }

              &.label {
                font-size: 14px;
                line-height: 20px;
                color: #8d8b92;
                font-family: "Averta";

                @media #{$mobile} {
                  font-size: 12px;
                }
              }
            }

            span {
              background-color: rgba(#08ae7e, 0.3);
              border-radius: 5px;
              color: #08ae7e;
              padding: 4px 11px;
              display: inline-block;
              margin-top: 5px;

              @media #{$mobile} {
                font-size: 14px;
              }
            }

            .menu-btn {
              position: absolute;
              width: 32px;
              height: 32px;
              border-radius: 50%;
              line-height: 15px;
              text-align: center;
              top: 35px;
              right: 0px;
              background: #fefefe;
              border: 1px solid #efeee6;
              transition: all 0.3s ease-in-out;

              @media #{$mobile} {
                top: 47px;
                border: 0;
                background: none;
              }

              svg {
                path {
                  transition: all 0.3s ease-in-out;
                  fill: #b7b5ba;
                }
              }

              &:hover {
                border-color: #e1ddcf;

                svg {
                  path {
                    fill: #b7b5ba;
                  }
                }
              }
            }
          }

          .cancel {
            font-size: 14px;
            line-height: 20px;
            color: #5a5761;
            padding: 16px;
            background: #ffffff;
            box-shadow: 0px 20px 40px rgba(240, 238, 233, 0.8),
              0px 3px 8px #e6e4df;
            position: absolute;
            right: 0;
            top: 75px;
            display: none;
            transition: all 0.2s ease-in-out;
            font-family: "AvertaSemibold";
            border: 0;

            @media #{$mobile} {
              font-size: 12px;
              padding: 12px;
              right: 10px;
              z-index: 2;
            }
          }

          .button {
            position: absolute;
            top: 40px;
            right: 0;
            font-size: 14px;
            line-height: 40px;
            padding: 0px 13px;
            display: inline-block;

            @media #{$mobile} {
              top: 20px;
            }

            &:hover {
              color: #fff;
            }

            &.edit {
              background: #ffecf1;
              color: $primaryColor;
              font-family: "AvertaSemibold";
              padding: 0px 15px;
              border: 0;
            }
          }

          .link {
            font-size: 14px;
            line-height: 20px;
            text-decoration: underline !important;
            color: $primaryColor;
            margin-top: 15px;
            margin-bottom: 0;
            display: inline-block;

            @media #{$mobile} {
              font-size: 12px;
              margin-top: 5px;
            }
          }
        }
      }

      #cancelrenewal {
        .modal-dialog {
          width: 700px;

          @media #{$mobile} {
            width: auto;
          }

          .modal-content {
            padding: 0;

            @media #{$mobile} {
              text-align: center;
            }
          }

          .creditModal-header {
            h5 {
              font-size: 24px;
              line-height: 32px;
              color: #5b5863;
              width: 60%;
              margin-bottom: 30px;
              padding-top: 50px;
              padding-left: 40px;

              @media #{$mobile} {
                text-align: center !important;
                padding-left: 0;
                padding-top: 95px;
                font-size: 18px;
                line-height: 18px;
                width: 80%;
                padding-bottom: 25px;
                margin: auto;
              }
            }

            .close {
              top: 60px;
              right: 40px;

              @media #{$mobile} {
                top: 30px;
                left: 50%;
                right: auto;
                transform: translateX(-50%);
              }
            }
          }

          .modal-body {
            padding-left: 40px;
            padding-right: 39px;
            padding-top: 38px;
            padding-bottom: 38px;
            border-top: 1px solid #e3e0d3;

            @media #{$mobile} {
              padding: 23px 0 66px;
            }

            p {
              font-size: 18px;
              line-height: 28px;
              color: #5b5863;
              margin-bottom: 0;

              @media #{$mobile} {
                font-size: 14px;
                line-height: 24px;
                width: 80%;
                margin: auto;
              }
            }
          }

          .cancel-btns {

            .confirm-cancellation,
            .keep-automatic-renewal {
              width: 50%;
              padding: 20px;
              float: left;

              @media #{$mobile} {
                width: 100%;
                display: block;
                float: none;
                font-size: 16px;
              }
            }

            .keep-automatic-renewal {
              color: $primaryColor;
              background-color: #ffecf1;
            }
          }
        }
      }
    }

    .back-to-dashboard {
      padding: 32px 60px;
      background-color: #f5f4f0;
      text-align: right;

      @media #{$mobile} {
        padding: 10px;
      }

      @media #{$tablet} {
        padding: 30px 40px;
      }

      a {
        padding: 16px 65px;
        display: inline-block;

        @media #{$mobile} {
          width: 100%;
          display: block;
          padding: 16px 0;
          font-size: 16px;
          text-align: center;
        }

        &:hover {
          color: #fff;

          i {
            left: -12px;
          }
        }

        i {
          position: relative;
          transition: all 0.3s ease-in-out;
          left: -7px;
          top: 0;
        }
      }
    }
  }

  .content-four {
    .change-password {
      background-color: #fff;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      margin-top: 80px;
      margin-bottom: 140px;

      @media #{$mobile} {
        margin-top: 20px;
        margin-bottom: 40px;
      }

      @media #{$tablet} {
        margin-top: 50px;
        margin-bottom: 80px;
      }

      &-content {
        padding: 60px;
        border-right: 1px solid #e3e0d3;

        @media #{$mobile} {
          padding: 24px;
        }

        @media #{$tablet} {
          padding: 30px;
        }

        form {
          .form-group {
            border: 1px solid #e0ded5;
            padding: 16px 30px;
            margin-bottom: 20px;

            @media #{$mobile} {
              padding: 5px 16px;
              height: 60px;
              margin-bottom: 8px;
            }

            label {
              font-size: 12px;
              line-height: 20px;
              font-family: "AvertaSemibold";
              margin-bottom: 0;
            }

            input {
              border: 0;
              padding-left: 0;
              padding: 0;

              @media #{$mobile} {
                font-size: 14px;
              }

              &::placeholder {
                font-size: 16px;
                line-height: 20px;

                @media #{$mobile} {
                  font-size: 14px;
                }
              }
            }
          }

          button {
            width: 100%;
            display: block;
            padding-top: 16px;
            padding-bottom: 16px;

            @media #{$mobile} {
              margin-top: 165px;
              font-size: 16px;
            }
          }
        }
      }
    }

    .check-password {
      padding: 60px;
      padding-left: 30px;

      @media #{$mobile} {
        padding: 25px 25px 15px;
        position: absolute;
        top: -247px;
        z-index: 5;
      }

      @media #{$tablet} {
        padding: 30px 0;
      }

      h4 {
        font-size: 16px;
        line-height: 20px;
        color: #5b5863;
        font-family: "AvertaSemibold";
        margin-bottom: 11px;
        width: 70%;

        @media #{$mobile} {
          width: 100%;
          font-size: 14px;
        }

        @media #{$tablet} {
          width: 75%;
        }

        @media #{$betweentabdesk} {
          width: 100%;
        }
      }

      ul {
        li {
          font-size: 13px;
          line-height: 32px;
          color: #8d8b92;

          i {
            margin-right: 8px;
            font-size: 16px;
            position: relative;
            top: 1px;
          }

          &.invalid {
            i {
              color: #5b5863;
            }
          }

          &.valid {
            i {
              color: #08ae7e;
            }
          }
        }
      }
    }
  }

  .content-five {
    padding-top: 60px;

    @media #{$mobile} {
      padding-top: 20px;
    }

    .weplay-content,
    .thanks-for-submission {
      background: #ffffff;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      padding: 60px;

      .weplay-title {
        font-size: 24px;
        line-height: 32px;
        text-align: center;
        margin-bottom: 35px;

        @media #{$mobile} {
          font-size: 18px;
          margin-bottom: 10px;
        }

        span {
          color: $primaryColor;
        }
      }

      @media #{$mobile} {
        padding: 24px;
      }

      @media #{$tablet} {
        padding: 50px;
      }

      @media #{$betweentabdesk} {
        padding: 60px 50px 50px;
      }

      label {
        margin-bottom: 4px;

        @media #{$mobile} {
          margin-bottom: 0;
          line-height: 1.5;
        }
      }

      input,
      select {
        border: 0;
        padding-left: 0;
        padding: 0;

        @media #{$mobile} {
          font-size: 14px;
        }

        &::placeholder {
          font-size: 16px;
          line-height: 20px;

          @media #{$mobile} {
            font-size: 14px;
          }
        }
      }

      .form-group {
        border: 1px solid #e0ded5;
        padding: 15px 30px;
        margin-bottom: 20px;

        @media #{$mobile} {
          padding: 5px 16px 9px;
          margin-bottom: 8px;
        }

        label {
          font-size: 12px;
          font-family: "AvertaSemibold";
          margin-bottom: 0;
        }
      }

      .submit {
        button {
          display: block;
          width: 100%;
          padding: 16px 0 15px;

          @media #{$mobile} {
            font-size: 16px;
          }
        }
      }
    }

    .thanks-for-submission {
      display: none;

      svg {
        @media #{$mobile} {
          width: 50px;
          height: 50px;
        }
      }

      h3 {
        font-size: 24px;
        line-height: 32px;
        margin-top: 30px;

        @media #{$mobile} {
          font-size: 18px;
          line-height: 24px;
          margin-top: 20px;
        }
      }

      p {
        font-size: 16px;
        line-height: 28px;
        margin: 15px 0 45px;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 20px;
          margin: 10px 0 25px;
        }
      }

      button {
        width: 100%;
        background-color: #08ae7e;
        font-size: 20px;
        text-align: center;
        color: #fcf9f2;
        padding: 13px 0;
        border: 0;

        @media #{$mobile} {
          font-size: 16px;
          padding: 16px 0;
        }
      }
    }

    .weplay-learn {
      margin-top: 30px;
      padding-bottom: 80px;
      font-size: 14px;
      line-height: 17px;
      color: #8d8b92;

      @media #{$mobile} {
        margin-top: 20px;
        padding-bottom: 40px;
      }
    }
  }
}