.hero-img.mac {
  .hero-img-image {
    img {
      top: 80px;

      @media #{$mobile} {
        top: 0;
      }

      @media #{$tablet} {
        top: 30px;
      }

      @media #{$betweentabdesk} {
        top: 130px;
      }
    }
  }
}

.hero-img.mac .header-shape.windows {
  @media #{$mobile} {
    top: -685px;
  }

  @media (max-width: 350px) {
    top: -715px;
  }

  @media #{$tablet} {
    top: -550px;
    right: -660px;
    transform: scale(0.9);
  }

  @media #{$betweentabdesk} {
    top: -580px;
    right: -745px;
  }
}

.benefits-wrapper.mac {
  .benefits-content {
    @media #{$mobile} {
      margin-top: 0;
    }
  }
}

.benefits .benefits-wrapper .benefit.mac .benefits-img {
  @media #{$mobile} {
    margin-top: 60px;
    margin-bottom: 20px;
  }

  img {
    left: 92px;

    @media #{$mobile} {
      left: -64px;
      top: -102px;
    }

    @media #{$tablet} {
      left: 0;
    }

    @media #{$betweentabdesk} {
      left: -10px;
      top: -10px;
    }
  }
}

.benefits .benefits-wrapper .benefit.mac .benfits-img-two {
  bottom: 155px;
  left: 270px;
  box-shadow: -90px 120px 149px rgba(186, 185, 181, 0.54);

  @media #{$mobile} {
    bottom: -65px;
    left: 38px;
  }

  @media #{$tablet} {
    bottom: 40px;
    left: 250px;
  }

  @media #{$betweentabdesk} {
    bottom: 75px;
    left: 110px;
  }
}