.wrapper-header.contact {
  padding-bottom: 134px;

  @media #{$mobile} {
    padding-bottom: 40px;
  }
}

/* Start Contact */
.contact-us {
  .contact-us-content {
    textarea {
      height: 100px;
      overflow: hidden;

      @media #{$mobile} {
        height: 80px;
      }
    }

    .form-group.file {
      height: 60px;
      padding: 0;
    }

    .custom--file {
      position: relative;
      height: 100%;

      img {
        position: absolute;
        top: 20px;
        left: 30px;
        z-index: 1;
        cursor: pointer;

        @media #{$mobile} {
          left: 10px;
        }
      }

      span {
        position: absolute;
        top: 17px;
        left: 50px;
        font-size: 14px;
        color: #8d8b92;
        z-index: 1;
        cursor: pointer;

        @media #{$mobile} {
          left: 25px;
          font-size: 12px;
          top: 20px;
        }
      }

      input[type="file"] {
        background-color: transparent;
        z-index: 2;
        position: relative;
        cursor: pointer;
        height: 100%;
      }

      button {
        position: absolute;
        right: 8px;
        top: 8px;
        z-index: 1;
        background-color: #f0eee8;
        font-size: 16px;
        text-align: center;
        color: #5b5863;
        border: 0;
        padding: 10px 13px;
        font-family: "AvertaSemibold";

        @media #{$mobile} {
          font-size: 14px;
          top: 12px;
          padding: 7px 9px;
        }
      }
    }

    .submit {
      button {
        background-color: $primaryColor;
        width: 100%;
        padding: 16px 30px;
        border: 0;
        position: relative;
        text-align: left;
        font-size: 20px;
        color: #fcf9f2;
        font-family: "AvertaSemibold";

        @media #{$mobile} {
          font-size: 16px;
        }

        svg {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 30px;
        }
      }
    }
  }
}

/* End Contact */