header.get-started-page {
  background-color: #fff;

  @media #{$mobile} {
    height: 76px;

    .navbar {
      padding: 20px;
    }
  }

  .get-started-login {
    p {
      font-size: 14px;
      line-height: 17px;
      color: #8d8b92;
      font-family: "Averta";

      @media #{$mobile} {
        font-size: 12px;
        line-height: 15px;
      }

      a {
        color: $primaryColor;
        margin-left: 5px;
        font-size: 16px;
        text-decoration: underline !important;
        font-family: "AvertaSemibold";

        i {
          font-weight: bold;
          transition: all 0.3s ease-in-out;
          margin-left: 4px;
        }

        @media #{$mobile} {
          display: block;
          text-align: right;
          font-size: 14px;
          font-family: "AvertaSemibold";
          transition: all 0.3s ease-in-out;
        }

        &:hover {
          color: $linkHoverColor;
          text-decoration: none !important;

          i {
            transform: translateX(2px);
          }
        }
      }
    }
  }
}

/* Start Choose Plan */
.choose-plan {
  background-color: #fff;

  &-info {
    ul {
      margin-top: 80px;
      margin-bottom: 42px;

      @media #{$mobile} {
        margin-top: 35px;
        margin-bottom: 25px;
        display: flex;
      }

      li {
        font-size: 18px;
        line-height: 32px;
        display: inline-block;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 16px;
          margin-right: 0;
          padding-left: 50px;
          position: relative;
        }

        &:first-child {
          margin-right: 32px;

          @media #{$mobile} {
            margin-right: 20px;
          }
        }

        img {
          margin-right: 16px;

          @media #{$mobile} {
            margin-right: 0;
            position: absolute;
            left: 0;
            top: -3px;
          }
        }
      }
    }

    h2 {
      font-size: 56px;
      line-height: 56px;
      margin-bottom: 80px;

      @media #{$mobile} {
        font-size: 36px;
        line-height: 44px;
        margin-bottom: 40px;
      }

      @media #{$betweentabdesk} {
        width: 70%;
      }
    }
  }
}

/* End Choose Plan */

/* Start Choose Steps */
.choose-steps {
  padding-top: 80px;
  padding-bottom: 130px;
  background-color: #f1f1ed;

  @media #{$mobile} {
    padding-top: 30px;
    padding-bottom: 10px;
  }

  @media #{$tablet} {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .step-no {
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 4px;
    color: #5b5863;
    opacity: 0.7;
    font-family: "AvertaSemibold";

    @media #{$mobile} {
      font-size: 12px;
      margin-bottom: 5px;
      padding-left: 20px;
    }

    @media #{$tablet} {
      font-size: 15px;
    }
  }

  .choose-title {
    font-size: 36px;
    line-height: 44px;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      width: 75%;
      margin-bottom: 20px;
      padding-left: 20px;
    }

    @media #{$tablet} {
      font-size: 26px;
      line-height: 36px;
    }

    small {
      font-size: 16px;
      line-height: 44px;
      color: #8d8b92;
      font-family: "Averta";

      @media #{$mobile} {
        font-size: 14px;
        line-height: 24px;

        &.have-account {
          display: block;
        }
      }

      a {
        color: $primaryColor;
        text-decoration: underline !important;
        font-family: "AvertaSemibold";

        i {
          font-weight: bold;
          transition: all 0.3s ease-in-out;
          margin-left: 4px;
        }

        &:hover {
          color: $linkHoverColor;
          text-decoration: none !important;

          i {
            transform: translateX(2px);
          }
        }
      }
    }

    img {
      @media #{$mobile} {
        transform: scale(0.6);
        position: absolute;
        margin-left: -15px;
      }
    }

    &.payment-method-title {
      width: 100%;
    }
  }

  .choose-steps-list {
    @media #{$mobile} {
      padding-left: 20px;
    }

    li {
      display: inline-block;
      font-size: 18px;
      line-height: 32px;
      margin-right: 24px;

      @media #{$mobile} {
        font-size: 16px;
        line-height: 32px;
        margin-right: 0;
        display: block;
      }

      @media #{$tablet} {
        display: block;
      }

      i {
        color: #08ae7e;
        margin-right: 8px;

        @media #{$mobile} {
          margin-right: 4px;
        }
      }
    }
  }

  .pricing-tables {
    padding-bottom: 70px;
    padding-top: 58px;

    @media #{$mobile} {
      padding-top: 20px;
      padding-bottom: 40px;
    }

    .price-card .label-radio {
      padding-bottom: 0;
    }
  }

  .create-account {
    margin-top: 30px;

    .step {
      .form-group {
        background-color: #fff;
        padding: 12px 30px 18px;
        height: 80px;
        margin-bottom: 0;
        border: 1px solid transparent;
        transition: all 0.3s ease-in-out;

        &:hover {
          border: 1px solid #e3e0d7;
        }

        @media #{$mobile} {
          margin-bottom: 8px;
          height: 60px;
          padding: 4px 16px;
        }

        label {
          font-size: 12px;
          line-height: 20px;
          font-family: "AvertaSemibold";
          margin-bottom: 0;
        }

        input {
          border: 0;
          padding-left: 0;
          padding: 0;
          background: none;
          margin-top: -5px;

          @media #{$mobile} {
            font-size: 14px;
          }

          &::placeholder {
            font-size: 16px;

            @media #{$maxTab} {
              font-size: 14px;
            }
          }
        }
      }
    }

    .data {
      font-size: 14px;
      line-height: 16px;
      color: #8d8b92;
      margin-top: 20px;
      margin-bottom: 72px;

      @media #{$mobile} {
        font-size: 12px;
        padding-left: 16px;
        margin-top: 6px;
        margin-bottom: 38px;
      }
    }
  }

  .payment {
    margin-top: 28px;

    @media(min-width: 1600px) {
      .padd-left {
        padding-left: 25px;
      }
    }

    .license {
      background: #ffffff;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      padding: 50px;

      @media #{$mobile} {
        padding: 36px;
        box-shadow: none;
        margin-top: 20px;
      }

      @media (max-width: 350px) {
        padding: 20px;
      }

      @media #{$tablet} {
        padding: 20px;
      }

      h3 {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 20px;

        @media #{$maxTab} {
          width: 90%;
        }
      }

      ul {
        li {
          font-size: 18px;
          line-height: 20px;
          margin-bottom: 20px;
          padding-left: 20px;
          position: relative;

          @media #{$mobile} {
            font-size: 14px;
            margin-bottom: 15px;
          }

          @media #{$tablet} {
            font-size: 16px;
            margin-bottom: 15px;
          }

          i {
            color: #08ae7e;
            position: absolute;
            left: 0;
            top: 2px;

            @media #{$maxTab} {
              font-size: 16px;
            }
          }
        }
      }
    }

    .back-btn {
      margin-top: 10px;

      @media #{$mobile} {
        text-align: center;
      }

      .back {
        font-size: 20px;
        line-height: 25px;
        font-family: "AvertaSemibold";
        color: $primaryColor;
        transition: all 0.3s ease-in-out;
        padding: 10px;
        display: inline-block;

        i {
          transition: all 0.3s ease-in-out;
        }

        @media #{$mobile} {
          font-size: 16px;
        }

        &:hover {
          color: $linkHoverColor;

          i {
            transform: translateX(-4px);
          }
        }
      }
    }
  }
}

.payment-methods {
  .payment--method {
    transition: all 0.3s ease-in-out;

    &:hover {
      box-shadow: 0px 6px 9px #e6e4df;
    }
  }

  .card-info {
    background-color: #fff;
    padding: 32px 32px 36px;
    border-top: 1px solid #e3e0d3;
    display: none;

    @media #{$maxTab} {
      padding: 20px 20px 35px;
    }

    @media #{$betweentabdesk} {
      padding: 20px 20px 35px;
    }

    .padd {
      padding-right: 10px;
      padding-left: 10px;

      @media #{$maxTab} {
        padding-right: 15px;
        padding-left: 15px;

        &.col-6 {
          padding-left: 5px;
        }
      }

      @media #{$betweentabdesk} {
        padding-right: 7.5px;
        padding-left: 7.5px;
      }
    }

    .padd-right {
      padding-right: 10px;

      @media #{$maxTab} {
        padding-right: 15px;

        &.col-6 {
          padding-right: 5px;
        }
      }

      @media #{$betweentabdesk} {
        padding-right: 7.5px;
      }
    }

    .padd-left {
      padding-left: 10px;

      @media #{$maxTab} {
        padding-left: 15px;
      }

      @media #{$betweentabdesk} {
        padding-left: 7.5px;
      }
    }

    form {
      .form-group {
        border: 1px solid #e0ded5;
        padding: 2px 20px 7px;
        margin-bottom: 17px;
        height: 60px;

        @media #{$maxTab} {
          margin-bottom: 10px;
          padding: 4px 16px;
        }

        @media #{$betweentabdesk} {
          padding: 2px 10px 7px;
          margin-bottom: 15px;
        }

        label {
          font-size: 12px;
          line-height: 20px;
          font-family: "AvertaSemibold";
          margin-bottom: 0;
          position: relative;

          @media #{$betweentabdesk} {
            font-size: 11px;
          }

          svg {
            cursor: pointer;
            display: inline-block;
            margin-left: 5px;
            position: relative;
            z-index: 2;

            path {
              transition: all 0.3s ease-in-out;
            }

            &:hover {
              &~.tooltiptext {
                opacity: 1;
                visibility: visible;
                top: -93px;
              }

              path {
                fill: #908e91;
              }
            }
          }

          .tooltiptext {
            position: absolute;
            display: inline-block;
            background: #4f4f4f;
            box-shadow: 0px 6px 11px rgba(79, 79, 79, 0.3);
            width: 200px;
            height: 86px;
            font-size: 12px;
            line-height: 14px;
            color: #ffffff;
            padding: 12px 16px;
            top: -98px;
            left: 25px;
            font-family: "Averta";
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease-in-out;

            &:after {
              content: "";
              position: absolute;
              border-width: 8px;
              border-style: solid;
              border-color: #4f4f4f transparent transparent transparent;
              display: block;
              bottom: -16px;
              left: 50%;
              transform: translateX(-50%);
              z-index: 1;
            }
          }
        }

        input {
          border: 0;
          padding: 0;
          font-family: "AvertaSemibold";
          font-size: 14px;
          margin-top: -7px;

          @media #{$maxTab} {
            line-height: 20px;
          }

          &::placeholder {
            font-family: "Averta";
            font-size: 14px;
          }
        }

        &~span {
          position: relative;
          font-size: 12px;
          color: #898691;
          display: inline-block;
          top: -17px;
        }
      }
    }

    .continue {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 32px;

      @media #{$maxTab} {
        margin-bottom: 20px;
      }

      strong {
        font-family: "AvertaBold";
      }
    }

    .all-coins {
      background: #F2F0E6;
      border: 1px solid #E3E0D3;
      border-radius: 10px;
      padding: 24px 47px;
      margin-bottom: 40px;

      @media #{$maxTab} {
        padding: 20px 32px 18px;
      }

      p {
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #8D8B92;
        margin-bottom: 20px;

        @media #{$mobile} {
          font-size: 13px;
        }
      }

      .coins {
        display: flex;
        justify-content: space-between;

        @media #{$maxTab} {
          justify-content: center;
          flex-wrap: wrap;
        }

        li {
          position: relative;
          cursor: pointer;

          @media #{$mobile} {
            margin-right: 7px;
            margin-bottom: 8px;
          }

          @media #{$tablet} {
            margin-right: 13px;
            margin-bottom: 8px;
          }

          &:before,
          &:after {
            position: absolute;
            visibility: hidden;
            opacity: 0;
            pointer-events: none;
            transition: all .2s cubic-bezier(.5, 1, .25, 1);
            z-index: 1
          }

          &:before {
            padding: 5px;
            width: 110px;
            border-radius: 3px;
            background: #4F4F4F;
            box-shadow: 0px 6px 11px rgba(79, 79, 79, 0.3);
            border-radius: 5px;
            color: #fff;
            content: attr(data-tooltip);
            text-align: center;
            font-size: 12px;
            font-family: "AvertaSemibold";
          }


          &:after {
            border: 8px solid transparent;
            width: 0;
            content: "";
            font-size: 0;
            line-height: 0;
          }

          &:hover:before,
          &:hover:after {
            visibility: visible;
            opacity: 1;
          }

          &.t-md:before {
            width: 140px
          }

          &.t-xs:before {
            width: 60px
          }

          &.t-top {

            &:before {
              bottom: 100%;
              left: 50%;
              margin-bottom: 5px;
              transform: translateX(-50%);
            }

            &:after {
              bottom: 100%;
              left: 50%;
              transform: translateX(-50%);
              border-top: 8px solid #4F4F4F;
              border-bottom: none;
            }

            &:hover:before,
            &:hover:after {
              transform: translateX(-50%) translateY(-5px);
            }
          }
        }
      }
    }

    .order-info {
      border-top: 1px dashed #e3e0d3;
      padding-top: 32px;

      @media #{$maxTab} {
        border: 0;
        padding-top: 14px;

        &.paypal-txt {
          border-top: 1px dashed #e3e0d3;
          padding-top: 20px;
        }
      }

      .order-info-content {
        .order-total {
          font-size: 18px;
          line-height: 24px;
          margin-bottom: 8px;

          .total {
            font-family: "AvertaBold";
          }
        }

        .bill {
          font-size: 14px;
          line-height: 14px;
          color: #8d8b92;

          @media #{$maxTab} {
            margin-bottom: 24px;
          }

          @media #{$betweentabdesk} {
            margin-bottom: 24px;
          }
        }
      }

      .secure-payment {
        @media #{$maxTab} {
          display: flex;
          justify-content: space-between;
        }

        li {
          font-size: 12px;
          line-height: 24px;
          position: relative;
          padding-left: 35px;
          margin-bottom: 10px;

          @media #{$maxTab} {
            line-height: 12px;
            padding-left: 30px;
            margin-bottom: 0;
          }

          div {
            width: 24px;
            height: 24px;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);

            img {
              max-width: 100%;
            }
          }
        }
      }

      .credit-card-submit,
      .paypal-btn {
        width: 100%;
        height: 56px;
        line-height: 56px;
        margin-top: 22px;
        margin-bottom: 15px;

        @media #{$maxTab} {
          font-size: 16px;
        }
      }

      .paypal-btn {
        background-color: #ffc63f;
        color: #44424a;
        transition: all 0.3s ease-in-out;

        &:hover {
          background-color: #ffd163;
        }

        img {
          position: relative;
          left: 3px;

          @media #{$maxTab} {
            transform: scale(0.67) translateX(-24px);
          }


          &.coin {
            top: -7px;
          }
        }

        &.coninpay,
        &.alipay-btn {
          img {
            transform: scale(1);
          }
        }
      }

      p.complete {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #8d8b92;

        @media #{$maxTab} {
          width: 240px;
          margin: auto;
        }

        a {
          font-family: "AvertaSemibold";
          color: $primaryColor;
          transition: all 0.3s ease-in-out;

          &:hover {
            color: $linkHoverColor;
            text-decoration: underline !important;
          }
        }
      }
    }
  }

  .payment--method {
    overflow: hidden;
    background-color: #fff;

    &:not(:first-child) {
      margin-top: 12px;

      @media #{$mobile} {
        margin-top: 8px;
        margin-bottom: 0;
      }
    }




    .pay-card {
      position: relative;

      .cards {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);

        li {
          margin-right: 5px;
          display: inline-block;

          @media #{$mobile} {
            margin-right: -12px;
            transform: scale(0.55);
          }

          @media #{$tablet} {
            margin-right: -7px;
            transform: scale(0.7);
          }

          &.more {
            background: #6B5B60;
            border: 1px solid #6B5B60;
            color: #fff;
            font-size: 14px;
            font-family: "AvertaSemibold";
            width: 28px;
            height: 28px;
            line-height: 24px;
            border-radius: 50%;
            cursor: pointer;
            text-align: center;
          }
        }

        &.coins {
          @media #{$maxTab} {
            right: 20px;
          }

          li {
            @media #{$maxTab} {
              margin-right: -8px;
              transform: scale(0.7);
            }
          }
        }
      }

      input[type="radio"] {
        display: none;

        &:not(:checked) {
          &+label:after {
            opacity: 0;
            transform: scale(0);
          }
        }

        &:checked {
          &+label:after {
            opacity: 1;
            transform: scale(1);
          }

          &+label:before {
            background-color: $primaryColor;
            border-color: $primaryColor;
          }
        }
      }

      .label-radio {
        font-size: 18px;
        text-transform: capitalize;
        position: relative;
        cursor: pointer;
        height: 80px;
        display: block;
        padding-left: 80px;
        line-height: 80px;
        font-family: "AvertaSemibold";
        margin-bottom: 0;
        z-index: 2;

        @media #{$mobile} {
          height: 48px;
          line-height: 48px;
          font-size: 14px;
          padding-left: 43px;
        }

        @media #{$tablet} {
          font-size: 16px;
          padding-left: 53px;
        }

        &:before {
          content: "";
          position: absolute;
          top: 25px;
          left: 32px;
          width: 32px;
          height: 32px;
          border-radius: 50%;
          border: 1px solid #adaba5;

          @media #{$mobile} {
            width: 20px;
            height: 20px;
            top: 14px;
            left: 16px;
          }

          @media #{$tablet} {
            width: 25px;
            height: 25px;
            top: 28px;
            left: 20px;
          }
        }

        &:after {
          content: "\f00c";
          position: absolute;
          top: 1px;
          left: 41px;
          color: #fff;
          font-family: "fontawesome";
          font-size: 14px;

          @media #{$mobile} {
            top: 0;
            left: 21px;
            font-size: 10px;
          }

          @media #{$tablet} {
            top: 0;
            left: 26px;
          }
        }
      }

      img {
        position: relative;
        z-index: 1;

        &.paypal-img {
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);

          @media #{$mobile} {
            transform: scale(0.7);
            right: 8px;
            top: 15px;
          }

          @media(min-width: 768) and (max-width: 1200px) {
            transform: scale(0.8);
            right: 8px;
            top: 30px;
          }
        }

        &.alipay-img {
          @media #{$mobile} {
            transform: scale(1) translate(-9px, -2px);
          }

        }
      }
    }
  }
}

/* End Choose Steps */