/* Start Help Center Search*/

header.help-center-page {
  background-color: #fff;
}

.help-center-search {
  padding-bottom: 88px;
  border-bottom: 1px solid #e8e4db;
  background-color: #fff;

  @media #{$mobile} {
    padding-bottom: 32px;
  }

  @media #{$tablet} {
    padding-bottom: 40px;
  }

  .help-center-content {
    h2 {
      font-size: 36px;
      line-height: 44px;
      padding-top: 74px;
      margin-bottom: 40px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        padding-top: 25px;
        margin-bottom: 25px;
      }

      @media #{$tablet} {
        padding-top: 60px;
        margin-bottom: 25px;
      }
    }

    form {
      border: 1px solid #ebe8df;
      box-sizing: border-box;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      position: relative;

      input {
        width: 100%;
        display: block;
        padding: 30px 40px;
        border: 0;
        font-size: 20px;

        @media #{$mobile} {
          font-size: 14px;
          padding: 17px 16px;
        }

        &::placeholder {
          font-size: 20px;

          @media #{$mobile} {
            font-size: 14px;
          }
        }
      }

      button {
        background-color: #fff;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        border: 0;
        cursor: pointer;
        width: 88px;
        height: 100%;

        @media #{$mobile} {
          width: 54px;

          svg {
            transform: scale(0.5);
          }
        }
      }
    }
  }
}

/* End Help Center Search*/

/* Start Help-feats */
.help-feats {
  background-color: #f5f4ef;

  .help-feats-content {
    padding-top: 80px;
    padding-bottom: 80px;

    @media #{$mobile} {
      padding-left: 10px;
      padding-right: 10px;
      padding-top: 25px;
      padding-bottom: 35px;

      .row {
        .box {
          padding: 0 5px;

          .feat {
            width: auto;
            height: 76px;
          }
        }
      }
    }

    @media #{$tablet} {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    .feat {
      text-align: center;
      border: 1px solid #e0ded5;
      background-color: #fff;
      box-shadow: 0px 10px 20px rgba(240, 238, 233, 0.4);
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      height: 173px;
      width: 350px;
      transition: all 0.3s ease-in-out;

      &:hover {
        box-shadow: none;
        color: #5b5863;
        border-color: transparent;
      }

      @media #{$tablet} {
        width: auto;
        height: 135px;
      }

      @media #{$betweentabdesk} {
        width: auto;
      }

      img {
        @media #{$mobile} {
          transform: scale(0.4);
        }

        @media #{$tablet} {
          transform: scale(0.7);
        }
      }

      h3 {
        font-size: 20px;
        line-height: 20px;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 20px;
          position: relative;
          top: -10px;
          margin-top: 0 !important;
        }

        @media #{$tablet} {
          margin-top: 0 !important;
        }
      }
    }
  }
}

/* End Help-feats */

/* Start popular Tickests */
.popular-tickets {
  background-color: #f5f4ef;

  .popular-tickets-title {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      top: 50%;
      left: 0;
      background-color: #e8e4db;
      z-index: 1;
    }

    h2 {
      font-size: 20px;
      line-height: 20px;
      position: relative;
      width: 280px;
      margin: auto;
      background-color: #fcfbfa;
      z-index: 2;
    }

    @media #{$mobile} {
      font-size: 16px;
      background-color: #fff;
      width: 195px;
    }

    @media #{$tablet} {
      background-color: #fff;
    }
  }

  &.help-center-page {
    h2 {
      background-color: #f5f4ef;
    }

    .popular-tickets-list {
      @media #{$tablet} {
        padding: 0 30px 30px;
        margin-bottom: 0;
      }
    }
  }

  .popular-tickets-list {
    margin-top: 30px;
    padding: 0 150px 70px;
    margin-left: 50px;
    display: flex;
    flex-wrap: wrap;

    @media #{$tablet} {
      padding: 0 30px;
      margin-left: 90px;
    }

    @media #{$mobile} {
      width: 73%;
      margin: 20px auto 0;
      padding: 0;
      padding-bottom: 30px;
    }

    @media #{$betweentabdesk} {
      padding: 0 20px 50px;
    }

    li {
      width: 33.3%;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 10px;
      padding-right: 30px;

      @media #{$mobile} {
        width: 100%;
        padding-right: 0;
      }

      @media #{$tablet} {
        width: 50%;
      }

      a {
        font-family: "Averta";
      }
    }
  }
}

/* End popular Tickests */

/* Start Contacting */
.contacting {
  padding-top: 60px;
  padding-bottom: 60px;

  @media #{$mobile} {
    &.help-center-page {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  .contacting-item {
    background: #ffffff;
    border: 1px solid #ebe8df;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    position: relative;
    padding-left: 115px;
    padding-top: 35px;
    padding-bottom: 43px;

    @media #{$tablet} {
      padding: 0;
      box-shadow: none;
    }

    @media #{$betweentabdesk} {
      padding-left: 95px;
    }

    @media #{$mobile} {
      padding: 0;
      box-shadow: none;

      &.last {
        margin-top: 10px;
      }
    }

    img {
      position: absolute;
      top: 29px;
      left: 32px;

      &.ticket {
        margin-top: 10px;
      }

      @media #{$mobile} {
        transform: scale(0.55);
        top: 12px;
        left: 68px;
      }

      @media #{$betweentabdesk} {
        left: 20px;
        top: 23px;
      }

      @media #{$tablet} {
        transform: scale(0.8);
        top: 16px;

        &.ticket {
          margin-top: 0;
          top: 30px;
        }
      }
    }

    .contacting-item-info {
      @media #{$mobile} {
        padding: 22px 70px 22px 127px;
      }

      @media #{$tablet} {
        padding-left: 100px;
        padding-top: 29px;
        padding-bottom: 29px;
      }

      p {
        font-size: 16px;
        line-height: 20px;
        color: #8d8b92;

        @media #{$mobile} {
          font-size: 12px;
          margin-bottom: 0;
        }

        @media #{$betweentabdesk} {
          margin-bottom: 10px;
          font-size: 15px;
        }

        @media #{$tablet} {
          font-size: 14px;
          margin-bottom: 2px;
        }
      }

      h4 {
        font-size: 20px;
        line-height: 20px;

        @media #{$mobile} {
          font-size: 14px;
          margin-bottom: 0;
        }

        @media #{$tablet} {
          font-size: 18px;
          margin-bottom: 0;
        }

        @media #{$betweentabdesk} {
          font-size: 18px;
          margin-bottom: 0;
        }
      }
    }

    .contacting-item-btn {
      position: absolute;
      top: 32px;
      right: 32px;

      @media #{$maxTab} {
        position: static;

        margin-top: 1px;
      }

      @media #{$betweentabdesk} {
        right: 20px;
      }

      a {
        background-color: #f0eee8;
        border: 0;
        font-family: "AvertaSemibold";
        padding: 16px 43px;
        color: #5b5863;
        display: inline-block;

        @media #{$maxTab} {
          font-size: 14px;
          display: block;
          width: 100%;
          text-align: center;
        }

        @media #{$tablet} {
          font-size: 16px;
        }

        @media #{$betweentabdesk} {
          padding: 16px 20px;
        }
      }
    }
  }
}

/* End Contacting */