.single {
  padding-top: 50px;

  @media #{$mobile} {
    padding-top: 0;
  }

  .container {
    .back-articles {
      margin-bottom: 84px;

      @media #{$mobile} {
        margin-bottom: 34px;
      }

      @media #{$tablet} {
        margin-bottom: 60px;
      }

      a {
        font-size: 16px;
        line-height: 20px;
        color: #8d8b92;
        text-decoration: underline !important;
        margin-left: 10px;
        transition: color 0.3s ease-in-out;

        @media #{$mobile} {
          font-size: 12px;
        }

        i {
          font-size: 12px;
          margin-right: 4px;
          transition: transform 0.3s ease-in-out;

          @media #{$mobile} {
            font-size: 10px;
          }
        }

        &:hover {
          color: $linkHoverColor;
          text-decoration: none !important;

          i {
            transform: translateX(-3px);
          }
        }
      }
    }

    .post-item {
      width: 100%;
      padding-bottom: 104px;

      @media #{$mobile} {
        padding-bottom: 60px;
      }

      @media #{$tablet} {
        padding-bottom: 80px;
      }

      .category {
        text-align: center;

        a {
          font-size: 14px;
          line-height: 17px;
          font-family: "AvertaSemibold";
          transition: color 0.3s ease-in-out;

          @media #{$mobile} {
            font-size: 12px;
          }

          &:hover {
            color: $primaryColor;
          }
        }
      }

      h1 {
        text-align: center;
        font-size: 56px;
        line-height: 56px;
        margin: 25px 0;

        @media #{$mobile} {
          font-size: 24px;
          line-height: 28px;
          margin: 17px 0 15px;
        }

        @media #{$tablet} {
          font-size: 44px;
          line-height: 46px;
          margin: 20px 0;
        }
      }

      .metabox {
        text-align: center;
        color: #8d8b92;

        @media #{$mobile} {
          font-size: 12px;
          line-height: 14px;
        }

        .author-img {
          background-image: url(../imgs/author1.png);
          width: 24px;
          height: 24px;
          border-radius: 50%;
          position: relative;
          top: 7px;
          margin-right: 3px;
        }
      }

      .post-content {
        margin-left: 50px;

        @media #{$mobile} {
          margin-left: 15px;
        }

        @media #{$tablet} {
          margin-left: 30px;
        }

        @media #{$betweentabdesk} {
          margin-left: 30px;
        }

        .post-desc {
          position: relative;
          margin-top: 40px;
          padding-left: 50px;
          padding-top: 30px;
          margin-bottom: 11px;

          @media #{$mobile} {
            margin-top: 0;
            padding-left: 40px;
          }

          &:before {
            content: "I";
            position: absolute;
            top: 0;
            left: -9px;
            font-family: "AvertaBold";
            font-size: 120px;
            line-height: 152px;
            color: $primaryColor;
          }
        }

        p {
          font-size: 18px;
          line-height: 32px;
          margin-bottom: 48px;

          @media #{$mobile} {
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 25px;
            padding-right: 15px;
          }

          @media #{$tablet} {
            font-size: 17px;
            line-height: 24px;
            margin-bottom: 32px;
          }
        }

        h2 {
          font-size: 36px;
          line-height: 44px;
          margin-bottom: 40px;

          @media #{$mobile} {
            font-size: 24px;
            line-height: 28px;
            margin-bottom: 15px;
          }

          @media #{$tablet} {
            font-size: 30px;
            line-height: 30px;
            margin-bottom: 20px;
          }
        }

        h3 {
          font-size: 24px;
          line-height: 36px;
          margin-bottom: 20px;

          @media #{$mobile} {
            font-size: 16px;
            line-height: 20px;
            margin-bottom: 10px;
          }

          @media #{$tablet} {
            font-size: 20px;
            line-height: 24px;
            margin-bottom: 15px;
          }
        }

        ol.post-list {
          list-style: decimal;
          padding-left: 20px;
          margin-bottom: 40px;

          li {
            font-size: 18px;
            line-height: 32px;

            @media #{$mobile} {
              font-size: 16px;
              line-height: 24px;
            }
          }
        }
      }

      .author-info {
        border-top: 1px solid #dedede;
        border-bottom: 1px solid #dedede;
        padding: 30px 0;
        overflow: hidden;
        text-align: left;
        margin-left: 50px;

        @media #{$mobile} {
          margin-top: 30px;
          padding-top: 25px;
          margin-left: 15px;
        }

        @media #{$tablet} {
          margin-left: 30px;
        }

        @media #{$betweentabdesk} {
          margin-left: 30px;
        }

        .author-img {
          float: left;
          width: 72px;
          margin-right: 20px;
          margin-top: 10px;
        }

        .author-txt {
          float: left;
          width: calc(100% - 92px);

          h4 {
            font-family: "Averta";
            font-size: 16px;
            line-height: 36px;
          }

          p {
            font-size: 14px;
            line-height: 24px;
            margin-bottom: 0;
            padding-right: 0;
          }
        }
      }

      .post-comment {
        margin-left: 50px;

        @media #{$mobile} {
          padding: 0 8px;
          margin-left: 15px;
        }

        @media #{$tablet} {
          margin-left: 30px;
        }

        @media #{$betweentabdesk} {
          margin-left: 30px;
        }

        .leave-comment {
          font-size: 13px;
          line-height: 16px;
          margin: 40px auto 27px;

          @media #{$mobile} {
            margin: 29px auto 15px;
          }

          span {
            font-family: "AvertaSemibold";
          }
        }

        form {
          position: relative;
          background: #ffffff;
          border: 1px solid #ebe8df;
          box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8),
            0px 6px 9px #e6e4df;
          padding: 0 10px;

          @media #{$mobile} {
            margin-bottom: 10px;
          }

          textarea {
            height: 207px;
            resize: none;
            border-radius: 0;
            outline: none;
            box-shadow: none;
            padding: 13px 10px;
            border: 0;

            @media #{$mobile} {
              height: 114px;
            }

            &::placeholder {
              font-size: 13px;
              line-height: 20px;
            }
          }

          button[type="submit"] {
            background: $primaryColor;
            font-size: 15px;
            line-height: 28px;
            text-align: center;
            color: #ffffff;
            padding: 8px 23px;
            border: 0;
            position: absolute;
            bottom: 20px;
            right: 20px;
            transition: all 0.3s ease-in-out;

            @media #{$mobile} {
              bottom: 10px;
              right: 0;
              left: 0;
              width: 100%;
              position: relative;
            }

            &:hover {
              background-color: $linkHoverColor;
            }
          }
        }

        .comment-first {
          width: 75%;
          margin: auto;
          overflow: hidden;
          position: relative;
          z-index: 2;

          @media #{$mobile} {
            width: 100%;
          }

          .meta-box {
            font-size: 11px;
            line-height: 36px;
            margin-bottom: 0;
            margin-top: 40px;

            @media #{$maxTab} {
              margin-top: 20px;
            }

            .avatar {
              display: inline-block;
              width: 32px;
              height: 32px;
              border-radius: 50%;
              background-color: #f0eee8;
              line-height: 32px;
              text-align: center;
              margin-right: 10px;
            }

            .author-name {
              font-size: 13px;
              line-height: 36px;
              font-family: "AvertaSemibold";
              margin-right: 6px;
              transition: color 0.3s ease-in-out;

              &:hover {
                color: $primaryColor;
              }
            }

            .reply {
              font-size: 11px;
              line-height: 36px;
              text-decoration-line: underline !important;
              color: $primaryColor;
              font-family: "AvertaSemibold";
              transition: color 0.3s ease-in-out;

              &:hover {
                text-decoration-line: none !important;
                color: $linkHoverColor;
              }
            }
          }

          .comment-text {
            font-size: 14px;
            line-height: 24px;
            margin-top: 15px;
            padding-bottom: 40px;
            padding-left: 45px;
            border-bottom: 1px solid #dedede;
            margin-bottom: 0;

            @media #{$maxTab} {
              margin-top: 4px;
              padding-bottom: 25px;
            }
          }

          .comment-second {
            width: 92%;
            float: right;

            .comment-text {
              border: 0;
            }
          }
        }
      }

      .single-slider {
        margin-top: 40px;

        @media #{$mobile} {
          margin-top: 25px;
        }

        .slider-item {
          background: #ffffff;
          border: 1px solid #e0ded5;
          box-shadow: 0px 10px 20px rgba(240, 238, 233, 0.4);
          padding: 28px 80px;
          position: relative;
          display: block;
          transition: all 0.3s ease-in-out;

          @media #{$mobile} {
            padding: 17px 65px;
          }

          &:hover {
            color: #5b5863;
            box-shadow: none;
            border-color: transparent;
          }

          .icon {
            position: absolute;
            top: 30px;
            width: 36px;
            height: 36px;
            line-height: 36px;
            text-align: center;
            background-color: #f0eee8;
            border-radius: 50%;

            @media #{$mobile} {
              top: 17px;
            }

            i {
              font-weight: bold;
            }
          }

          &.first {
            @media #{$mobile} {
              margin-bottom: 10px;
            }

            @media #{$tablet} {
              padding: 28px 0 28px 70px;
            }

            @media #{$betweentabdesk} {
              padding: 28px 0 28px 80px;
            }

            .prev {
              left: 28px;

              @media #{$mobile} {
                left: 16px;
              }

              @media #{$tablet} {
                left: 20px;
              }
            }
          }

          &.last {
            text-align: right;

            @media #{$tablet} {
              padding: 28px 70px 28px 0;
            }

            @media #{$betweentabdesk} {
              padding: 28px 80px 28px 0;
            }

            .next {
              right: 28px;

              @media #{$mobile} {
                right: 16px;
              }

              @media #{$tablet} {
                right: 20px;
              }
            }
          }

          h3 {
            font-size: 16px;
            line-height: 20px;
            margin-bottom: 8px;

            @media #{$mobile} {
              margin-bottom: 12px;
            }
          }

          p {
            font-size: 14px;
            line-height: 14px;
            margin-bottom: 0;
            color: #8d8b92;

            @media #{$mobile} {
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}

/* Start Knowledge */
.knowledge {
  background-color: #f5f4f0;
  padding: 104px 0;

  @media #{$mobile} {
    padding: 60px 0 30px;
  }

  @media #{$tablet} {
    padding: 80px 0 60px;
  }

  h2 {
    margin-bottom: 50px;

    @media #{$mobile} {
      margin-bottom: 30px;
    }
  }

  .item {
    background: #ffffff;
    border: 1px solid #e0ded5;
    box-sizing: border-box;
    box-shadow: 0px 10px 20px rgba(240, 238, 233, 0.4);
    padding: 35px 40px;

    @media #{$mobile} {
      padding: 24px;
      margin-bottom: 10px;
    }

    @media #{$tablet} {
      padding: 24px;
    }

    h3 {
      font-size: 16px;
      line-height: 20px;

      @media #{$tablet} {
        font-size: 15px;
      }
    }

    p {
      font-size: 13px;
      line-height: 14px;
      color: #8d8b92;
      margin-top: 15px;
      margin-bottom: 28px;

      @media #{$mobile} {
        margin-top: 10px;
        margin-bottom: 12px;
      }

      @media #{$tablet} {
        margin-top: 10px;
        margin-bottom: 15px;
      }
    }

    a {
      color: $primaryColor;
      text-decoration: underline !important;
      font-size: 16px;
      line-height: 20px;

      i {
        font-weight: bold;
        transition: all 0.3s ease-in-out;
        margin-left: 4px;
      }

      &:hover {
        text-decoration: none !important;
        color: $linkHoverColor;

        i {
          transform: translateX(3px);
        }
      }
    }
  }
}

/* End Knowledge */