.faq.faq-main-page {
  background-color: #f9f8f4;
  padding-top: 68px;
  padding-bottom: 134px;

  @media #{$mobile} {
    padding-top: 20px;
    padding-bottom: 35px;
  }

  @media #{$tablet} {
    padding-top: 50px;
    padding-bottom: 0;
  }

  h1 {
    font-size: 36px;
    line-height: 56px;
    margin-bottom: 25px;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 16px;
    }

    @media #{$tablet} {
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 20px;
    }
  }

  .faq-content {
    background-color: #f9f8f4;
    background-image: url(../imgs/faq-bg.png);
    background-size: 100%;

    @media #{$mobile} {
      background: none;
    }

    .accordion {
      padding-top: 50px;
      padding-bottom: 50px;
      background-color: #fff;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;

      @media #{$mobile} {
        background: none;
        box-shadow: none;
        padding: 0 5px;
      }
    }
  }
}