.hero-img.iphone {
  .header-shape.iphone {
    @media #{$betweentabdesk} {
      top: -560px;
    }
  }

  .hero-img-image.iphone {
    img {
      position: absolute;
    }

    @media #{$mobile} {
      height: 210px;
    }

    @media #{$tablet} {
      height: 470px;
    }

    .one {
      top: 75px;
      left: 280px;

      @media #{$mobile} {
        top: -130px;
        left: 41px;
        transform: scale(0.52);
      }

      @media (width: 425px) {
        top: -170px;
        left: 40px;
        transform: scale(0.47);
      }

      @media (max-width: 350px) {
        top: -90px;
        left: 52px;
        transform: scale(0.6);
      }

      @media #{$tablet} {
        top: -25px;
        left: 230px;
        transform: scale(0.8);
      }

      @media #{$betweentabdesk} {
        top: 55px;
        left: 170px;
        transform: scale(0.8);
      }
    }

    .two {
      top: 180px;
      left: 40px;

      @media #{$mobile} {
        top: -110px;
        left: -40px;
        transform: scale(0.41);
      }

      @media (width: 425px) {
        top: -107px;
        left: -20px;
      }

      @media (max-width: 350px) {
        left: -55px;
      }

      @media #{$tablet} {
        top: 78px;
        left: 60px;
        transform: scale(0.8);
      }

      @media #{$betweentabdesk} {
        top: 155px;
        left: 5px;
        transform: scale(0.8);
      }
    }
  }

  .hero-img-btn {
    padding: 24px 64.5px;
    margin-bottom: 10px;

    @media #{$mobile} {
      padding: 24px 0 23px;
    }
  }

  .google-play {
    display: inline-block;
    position: relative;

    @media #{$mobile} {
      display: block;
    }

    .google-play-btn {
      background-color: #2c3038;
      padding: 21px 50.5px;
      position: relative;
      top: -1px;
      margin-left: 15px;

      @media #{$mobile} {
        top: 0;
        margin-left: 0;
        display: block;
        text-align: center;
      }

      @media #{$betweentabdesk} {
        top: 0;
        margin-left: 0;
        padding: 21px 84.5px;
      }

      img:first-child {
        margin-right: 10px;
      }
    }
  }
}

.benefits .benefits-wrapper .benefit.iphone .benefits-img {
  @media #{$mobile} {
    margin-top: 20px;
  }

  overflow: visible;

  img {
    right: 45px;
    top: -80px;

    @media #{$mobile} {
      right: -115px;
      top: -220px;
      transform: scale(0.4);
    }

    @media #{$tablet} {
      right: -45px;
      top: -170px;
    }

    @media #{$betweentabdesk} {
      right: -35px;
      top: -180px;
    }
  }
}

.benefits .benefits-wrapper .benefit.iphone .benfits-img-two {
  border-radius: 50%;
  background-color: #e7e5e1;
  box-shadow: 100px 120px 149px rgba(186, 185, 181, 0.64);
  bottom: 198px;
  left: 35px;

  @media #{$mobile} {
    bottom: -80px;
    left: -20px;
    transform: scale(0.4);
  }

  @media #{$tablet} {
    bottom: 88px;
    left: 100px;
  }

  @media #{$betweentabdesk} {
    bottom: 122px;
    left: -15px;
  }
}