/* Start Hero Image */
.hero-img {
  position: relative;

  @media #{$mobile} {
    text-align: center;
    padding-top: 30px;
  }

  &-content {
    .hero-img-title {
      font-size: 80px;
      line-height: 88px;
      margin: 148px 0 20px;

      @media #{$mobile} {
        font-size: 36px;
        line-height: 44px;
        margin-top: 30px;
        width: 85%;
        margin: 30px auto 17px;
      }

      @media #{$tablet} {
        font-size: 45px;
        line-height: 1.5;
        margin: 100px auto 17px;
      }

      @media #{$betweentabdesk} {
        font-size: 60px;
        line-height: 75px;
      }
    }

    .hero-img-txt {
      font-size: 28px;
      line-height: 33px;

      @media #{$mobile} {
        font-size: 18px;
        line-height: 20px;
        margin-bottom: 0;
      }

      @media #{$tablet} {
        font-size: 22px;
        line-height: 22px;
      }

      @media #{$betweentabdesk} {
        font-size: 24px;
        line-height: 24px;
      }
    }

    .hero-img-btn {
      padding: 21px 43px;
      margin: 60px 0 17px;

      @media #{$mobile} {
        margin-top: 32px;
        font-size: 20px;
        width: 100%;
        padding: 24px;
        display: block;
        text-align: center;
      }

      @media #{$tablet} {
        margin-top: 32px;
      }
    }

    span {
      margin-left: 18px;
      display: block;
      font-size: 14px;
      line-height: 16px;
      color: #33303f;
      opacity: 0.6;

      @media #{$mobile} {
        margin-left: 0;

        i {
          font-size: 10px;
        }
      }
    }
  }

  .hero-img-image {
    position: relative;

    img {
      position: absolute;
      top: 50px;
      left: -10px;
      z-index: 1;

      @media #{$maxTab} {
        max-width: 100%;
        position: static;
      }

      @media #{$tablet} {
        top: 150px;
        position: absolute;
        left: 0;
      }

      @media #{$betweentabdesk} {
        top: 150px;
        left: 0;
        max-width: 100%;
      }
    }
  }

  .header-shape {
    position: absolute;
    top: -560px;
    right: -610px;
    z-index: -1;

    @media #{$mobile} {
      transform: scale(0.65);
      top: -690px;
      left: -130px;
    }

    @media #{$tablet} {
      top: -505px;
      right: -560px;
      transform: scale(0.75);
    }

    @media #{$betweentabdesk} {
      top: -470px;
      right: -500px;
      transform: scale(0.8);
    }
  }
}

/* End Hero Image */

/* Start Features */
.features {
  padding-top: 75px;
  overflow: hidden;

  @media #{$mobile} {
    padding-top: 35px;
  }

  .features-content {
    .feat {
      padding: 80px 60px 60px;
      background-color: #fff;
      width: 350px;
      margin: auto;
      transition: all 0.3s ease-in-out;

      &:hover {
        box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 3px 8px #e6e4df;
      }

      @media #{$tablet} {
        padding: 36px 10px 36px 20px;
        width: 100%;
      }

      @media #{$mobile} {
        margin-bottom: 16px;
        padding: 36px;
        width: 100%;
      }

      @media #{$betweentabdesk} {
        padding: 60px 40px;
        width: 100%;
      }

      &-hr {
        border-color: #ebe8df;
        margin-top: 60px;
        margin-bottom: 40px;

        @media #{$mobile} {
          margin-top: 32px;
          margin-bottom: 32px;
        }

        @media #{$tablet} {
          margin-top: 40px;
          margin-bottom: 30px;
        }
      }

      &-title {
        font-size: 20px;
        line-height: 20px;
        margin-bottom: 20px;

        @media #{$mobile} {
          font-size: 16px;
          line-height: 16px;
          margin-bottom: 16px;
        }

        @media #{$tablet} {
          font-size: 17px;
          line-height: 20px;
          margin-bottom: 15px;
        }
      }

      &-txt {
        font-size: 16px;
        line-height: 28px;

        @media #{$maxTab} {
          font-size: 14px;
          line-height: 24px;
        }
      }
    }
  }

  .feat-more {
    text-align: center;
    margin-top: 44px;
    padding-bottom: 100px;
    border-bottom: 1px solid rgba(#838088, 0.2);

    @media #{$mobile} {
      margin-top: 5px;
      padding-bottom: 0;
      border-bottom: 0;
    }

    @media #{$tablet} {
      padding-bottom: 50px;
    }

    a {
      color: $primaryColor;
      text-decoration: underline !important;
      transition: all 0.3s ease-in-out;
      position: relative;
      z-index: 2;

      i {
        margin-left: 4px;
      }

      &:hover {
        text-decoration: none !important;
        color: #e33866;
      }
    }
  }
}

/* End Features */

/* Start advantages */
.advantages {
  padding-top: 130px;
  padding-bottom: 200px;
  overflow: hidden;

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media #{$tablet} {
    padding-top: 0;
    padding-bottom: 60px;
  }

  @media #{$betweentabdesk} {
    padding-top: 60px;
    padding-bottom: 100px;
  }

  .section-one,
  .section-two,
  .section-three {
    padding-right: 15px;
    padding-left: 15px;

    @media #{$mobile} {
      padding-left: 0;
      padding-right: 0;
    }

    .advantages-content {
      @media #{$mobile} {
        padding-left: 10px;
      }

      .advantages-title {
        padding-top: 70px;
        font-size: 36px;
        line-height: 44px;

        @media #{$mobile} {
          padding-top: 10px;
          font-size: 24px;
          line-height: 28px;
          width: 70%;
          margin-bottom: 0;
        }

        @media #{$tablet} {
          padding-top: 100px;
          font-size: 30px;
          line-height: 36px;
        }
      }

      .advantages-desc {
        font-size: 18px;
        line-height: 28px;
        margin-top: 26px;
        margin-bottom: 40px;
        width: 75%;

        @media #{$maxTab} {
          font-size: 16px;
          line-height: 28px;
          width: 100%;
          margin-top: 24px;
          margin-bottom: 21px;
        }

        @media #{$tablet} {
          width: 100%;
        }
      }

      .advantages-btn {
        padding: 24px 48px 23px;
      }
    }

    .advantages-img {
      text-align: left;

      img {
        max-width: 100%;
      }
    }
  }

  .section-one {
    .advantages-img {
      @media #{$tablet} {
        padding-top: 100px;
      }

      @media #{$betweentabdesk} {
        padding-top: 60px;
      }
    }
  }

  .section-two {
    .advantages-content {
      .advantages-title {
        padding-top: 150px;
        width: 80%;

        @media #{$mobile} {
          padding-top: 55px;
          width: 90%;
        }

        @media #{$tablet} {
          width: 100%;
          padding-top: 110px;
        }
      }

      .advantages-desc {
        @media #{$mobile} {
          margin-bottom: 0;
        }
      }
    }

    .advantages-img {
      text-align: center;
      padding-top: 60px;

      img {
        max-width: 100%;
      }
    }
  }

  .section-three {
    padding-top: 100px;

    @media #{$maxTab} {
      padding-top: 50px;
    }

    .advantages-content {
      .advantages-title {
        width: 80%;
        padding-top: 20px;

        @media #{$mobile} {
          padding-top: 0;
          width: 65%;
        }

        @media #{$tablet} {
          width: 90%;
        }
      }

      .advantages-desc {
        @media #{$mobile} {
          margin-bottom: 26px;
        }
      }
    }

    .advantages-img {
      @media #{$mobile} {
        padding-bottom: 50px;
      }

      @media #{$tablet} {
        padding-top: 70px;
      }

      img {
        max-width: 100%;
      }
    }

    .advantages-icons {
      margin-bottom: 40px;

      @media #{$maxTab} {
        margin-bottom: 24px;
      }

      li {
        display: inline-block;
        margin: 0 3px;
        width: 28px;
        height: 28px;

        @media #{$mobile} {
          margin: 0;
          margin-right: 9px;
          width: 25px;
          height: 25px;
        }

        @media #{$tablet} {
          margin: 0 2px;
        }

        a {
          display: block;

          &:hover {
            svg {
              path {
                fill: $primaryColor;
              }
            }

            .firefox {
              .line {
                stroke: $primaryColor;
                fill: none;
              }

              .fox {
                fill: $primaryColor;
              }
            }
          }

          svg {
            transform: scale(0.6);

            path {
              transition: all 0.3s ease-in-out;
            }
          }
        }
      }
    }
  }
}

/* End advantages */

/* Start Servers */
.servers {
  background-color: #fff;
  padding-top: 155px;
  overflow: hidden;

  @media #{$tablet} {
    padding-top: 60px;
  }

  @media #{$betweentabdesk} {
    padding-top: 100px;
  }

  @media #{$mobile} {
    padding-top: 40px;

    .server-map-img {
      position: relative;
      min-height: 150px;

      img {
        max-width: 100%;
      }
    }
  }

  .servers-title {
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    width: 46%;
    margin: auto;
    margin-bottom: 80px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      width: 85%;
      margin-bottom: 25px;
    }

    @media #{$tablet} {
      font-size: 30px;
      line-height: 36px;
      width: 65%;
      margin-bottom: 40px;
    }

    @media #{$betweentabdesk} {
      width: 60%;
    }
  }

  .servers-content {
    background: url(../imgs/Map.png) center center no-repeat;
    padding-top: 165px;
    padding-bottom: 210px;
    margin: 0 65px;

    @media #{$betweentabdesk} {
      background-size: contain;
      padding-top: 95px;
      padding-bottom: 130px;
      margin: 0 20px;
    }

    @media #{$tablet} {
      background: none;
      position: relative;
      padding-top: 85px;
      padding-bottom: 100px;

      margin: 0;

      .map {
        position: absolute;
        top: -70px;
        left: -230px;
        transform: scale(0.7);
      }
    }

    @media #{$mobile} {
      background: none;
      padding-top: 0;
      padding-bottom: 0;
      margin: 0;
      border: 1px solid #f2f0e9;
      text-align: center;
      margin-bottom: 8px;

      .row {
        padding: 0 20px;
      }

      .col-4 {
        padding: 0;
      }
    }

    .server-feat {
      position: relative;
      padding-left: 132px;
      padding-top: 38px;
      padding-bottom: 38px;
      padding-right: 70px;
      width: 310px;
      margin: auto;
      background: #ffffff;
      box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      transition: all 0.3s ease-in-out;

      &:hover {
        box-shadow: 0px 5px 20px rgba(240, 238, 233, 0.8), 0px 2px 3px #e6e4df;
      }

      @media #{$mobile} {
        width: 100%;
        padding: 5px 20px 20px;
        box-shadow: none;
        height: 100%;
      }

      @media (max-width: 350px) {
        width: 100%;
        padding: 5px 10px 20px;
        box-shadow: none;
        height: 100%;
      }

      @media #{$tablet} {
        width: 100%;
        padding: 20px;
        text-align: center;
      }

      @media #{$betweentabdesk} {
        width: 100%;
        padding-left: 140px;
        padding-right: 30px;
      }

      img {
        position: absolute;

        @media #{$mobile} {
          position: static;
          margin-bottom: -5px;
          transform: scale(0.5);
          display: block;
        }

        @media #{$tablet} {
          position: static;
          margin-bottom: -5px;
          transform: scale(0.8);
          display: block;
          margin: auto;
        }
      }

      h2 {
        position: relative;
        font-size: 56px;
        line-height: 56px;
        display: inline-block;
        margin-bottom: 0;

        @media #{$mobile} {
          font-size: 28px;
          line-height: 28px;
          display: block;
          margin-bottom: 5px;
        }

        @media #{$tablet} {
          font-size: 36px;
          line-height: 44px;
          display: block;
        }

        span {
          position: absolute;
          top: 10px;
          right: -20px;
          font-size: 20px;
          line-height: 20px;
          color: #a29cb0;
          font-family: "Averta";

          @media #{$mobile} {
            font-size: 14px;
            line-height: 14px;
            right: -5px;
          }

          @media #{$tablet} {
            font-size: 17px;
            line-height: 17px;
            right: 35px;
          }
        }
      }

      span {
        font-size: 20px;
        line-height: 20px;
        display: block;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 14px;
        }

        @media #{$tablet} {
          font-size: 17px;
          line-height: 17px;
        }
      }

      &.one {
        @media #{$mobile} {
          border-right: 1px solid #f2f0e9;
        }

        img {
          top: 45px;
          left: 30px;

          @media #{$mobile} {
            position: relative;
            left: -10px;
            top: 0;
          }
        }
      }

      &.two {
        @media #{$mobile} {
          border-right: 1px solid #f2f0e9;
        }

        img {
          top: 50px;
          left: 50px;
        }
      }

      &.three {
        img {
          top: 50px;
          left: 50px;
        }
      }
    }
  }

  .server-btn {
    padding-bottom: 140px;

    @media #{$mobile} {
      padding-bottom: 24px;
    }

    @media #{$tablet} {
      padding-bottom: 60px;
    }

    @media #{$betweentabdesk} {
      padding-bottom: 100px;
    }

    a {
      padding: 21px 51px;

      @media #{$mobile} {
        padding: 24px 0 23px;
        display: block;
      }

      &:hover {
        background-color: $primaryColor;
        color: #fff;

        i {
          color: #fff;
        }
      }

      &:active {
        background-color: #ff5784;
      }
    }
  }
}

/* End Servers */

/* Start Testimonials */
.testimonials {
  padding-bottom: 110px;
  background-color: #f9f8f4;

  &.homepage {
    padding-bottom: 120px;
  }

  @media #{$mobile} {
    .no-padding {
      padding: 0;
    }

    .container {
      padding: 0 15px;
    }
  }

  @media #{$tablet} {
    padding-bottom: 60px;

    .row {
      margin-right: -40px;
    }
  }

  .testimonials-text {
    font-size: 20px;
    line-height: 20px;
    opacity: 0.7;
    margin-bottom: 15px;
    padding-left: 35px;
    padding-top: 140px;

    @media #{$mobile} {
      padding-left: 0;
      font-size: 14px;
      line-height: 20px;
      padding-top: 60px;
      text-align: center;
      margin-bottom: 9px;
    }

    @media #{$tablet} {
      padding-left: 20px;
      padding-top: 60px;
    }

    @media #{$betweentabdesk} {
      padding-left: 0;
    }

    span {
      font-family: "AvertaBold";
    }
  }

  .testimonials-title {
    padding-left: 35px;
    font-size: 36px;
    line-height: 44px;

    @media #{$mobile} {
      padding-left: 0;
      font-size: 24px;
      line-height: 28px;
      text-align: center;
    }

    @media #{$tablet} {
      padding-left: 20px;
      font-size: 30px;
      line-height: 36px;
    }

    @media #{$betweentabdesk} {
      padding-left: 0;
    }
  }

  .testimonials-tabs {
    padding-top: 70px;
    padding-right: 50px;

    @media #{$mobile} {
      padding-top: 65px;
      padding-right: 0;
      border-bottom: 1px solid #ebe8e0;
      margin: 0 6px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    @media #{$tablet} {
      padding-top: 60px;
      padding-right: 0;
    }

    @media #{$betweentabdesk} {
      padding-right: 0;
    }

    li {
      border-bottom: 1px solid #ebe8e0;
      font-size: 20px;
      padding-bottom: 32px;
      padding-top: 32px;

      @media #{$mobile} {
        display: inline-block;
        border: 0;
        position: relative;
        padding: 0;
        font-size: 16px;
        padding: 0 2px;
      }

      &:first-child {
        padding-top: 0;
      }

      a {
        color: #5b5863;
        padding: 10px 0;

        @media #{$mobile} {
          padding: 10px 12px;
          display: block;
        }
      }

      &.active {
        a {
          color: $primaryColor;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 68px;
            width: 100%;
            height: 2px;
            background-color: $primaryColor;

            @media #{$mobile} {
              top: 42px;
            }
          }
        }

        .review {
          background-color: #ffecf1;
          color: $primaryColor;

          img {
            filter: none;

            @media #{$mobile} {
              transform: scale(0.84);
            }
          }
        }
      }

      .review {
        background-color: #ebe9e1;
        border-radius: 40px;
        padding: 6px 13px;
        margin-left: 8px;
        font-size: 16px;

        @media #{$mobile} {
          position: absolute;
          top: -18px;
          left: 50%;
          width: 50px;
          transform: translateX(-50%);
          font-size: 13px;
          margin-left: 0;
          padding: 2px;
          text-align: center;
        }

        img {
          position: relative;
          top: -2px;
          filter: grayscale(100%);

          @media #{$mobile} {
            transform: scale(0.84);
          }
        }
      }
    }
  }

  .testimonials-slider {
    .slider {

      &.content-three,
      &.content-two {
        opacity: 0;
        transition: all 0.15s ease-in-out;

        @media #{$mobile} {
          display: none;
        }
      }

      @media #{$mobile} {
        height: 120px;

        .container {
          padding: 0;
        }
      }

      @media #{$tablet} {
        .container {
          padding-right: 0;
        }
      }

      .owl-carousel {
        z-index: 1;
        position: absolute;
        width: 1470px;
        right: 0;
        left: 30px;
        top: 70px;

        @media #{$maxTab} {
          left: 20px;
          width: 100%;
          top: 44px;
          // position: static;
        }

        @media #{$betweentabdesk} {
          width: 100%;
          left: 30px;
        }

        .owl-stage-outer {
          padding-bottom: 50px;
          padding-left: 20px;
        }

        .testim {
          box-shadow: 10px 10px 50px -20px #e6e4df, -10px 6px 20px -15px #e6e4df;
          background-color: #fff;
          padding: 67px 0 55px 60px;
          min-height: 540px;

          @media #{$mobile} {
            padding: 32px 0 55px 24px;
            min-height: 400px;
            box-shadow: 10px 10px 50px -37px #e6e4df,
              -10px 6px 20px -13px #e6e4df;
          }

          @media #{$tablet} {
            padding: 40px 0 55px 30px;
            min-height: 480px;
          }

          img {
            width: auto;
          }

          .quote {
            margin-bottom: 65px;

            @media #{$mobile} {
              margin-bottom: 25px;
            }

            @media #{$tablet} {
              margin-bottom: 30px;
            }
          }

          .review {
            position: relative;

            .review-img {
              display: inline-block;
              position: relative;
              top: -4px;
            }

            span {
              font-size: 16px;
              line-height: 28px;
              margin-left: 5px;
            }

            &:after {
              content: "";
              position: absolute;
              right: 0;
              width: 160px;
              height: 1px;
              background-color: #ebe8df;
              top: 15px;

              @media #{$mobile} {
                width: 140px;
              }
            }
          }

          .testim-txt {
            font-size: 16px;
            line-height: 28px;
            margin-top: 32px;
            margin-bottom: 57px;
            width: 80%;

            @media #{$mobile} {
              margin-top: 35px;
              margin-bottom: 20px;
              width: 92%;
              font-size: 14px;
            }
          }

          .testim-author {
            h4 {
              font-size: 16px;
              line-height: 20px;

              span {
                width: 36px;
                height: 36px;
                display: inline-block;
                border-radius: 50%;
                margin-right: 12px;
                background-color: #ffedf2;
                color: $primaryColor;
                text-align: center;
                line-height: 36px;
              }

              img {
                display: inline-block;
                margin-right: 12px;
              }
            }
          }
        }
      }
    }
  }

  // owl nav format
  .owl-theme .owl-nav {
    bottom: 50px;
    margin-top: 0;
    position: absolute;
    left: -319px;

    @media #{$mobile} {
      left: 10px;
      bottom: -40px;
    }

    @media #{$tablet} {
      left: -250px;
    }

    @media #{$betweentabdesk} {
      left: -265px;
    }
  }

  .owl-carousel .owl-nav button.owl-next,
  .owl-carousel .owl-nav button.owl-prev {
    background-color: #f0eee9;
    width: 72px;
    height: 72px;
    line-height: 72px;
    text-align: center;
    margin: 0 10px;
    border-radius: 0;
    transition: all 0.3s ease-in-out;

    @media #{$mobile} {
      height: 44px;
    }

    &:hover {
      background-color: #e5e4df;

      i {
        color: #3d3b42;
      }
    }

    i {
      color: #5b5863;
      display: block;
      font-size: 24px;
      transition: color 0.3s ease-in-out;
    }
  }
}

/* End Testimonials */

/* Start Authority */
.authority {
  background-color: #fff;
  padding-top: 275px;

  @media #{$tablet} {
    padding-top: 220px;
  }

  .authority-content {
    padding-top: 80px;
    padding-bottom: 160px;

    @media #{$mobile} {
      padding-bottom: 60px;
    }

    @media (max-width: 350px) {
      padding-top: 170px;
    }

    @media #{$tablet} {
      padding-bottom: 100px;
    }

    .authority-img {
      text-align: center;
    }

    .authority-info {
      position: relative;
      padding-top: 60px;
      padding-left: 25px;

      @media #{$mobile} {
        padding-left: 0;
        padding-top: 30px;
      }

      @media #{$betweentabdesk} {
        padding-top: 0;
      }

      ul {
        padding-bottom: 60px;
        margin-bottom: 45px;
        position: relative;

        @media #{$maxTab} {
          display: flex;
          justify-content: space-between;
          padding-bottom: 35px;
          margin-bottom: 25px;
        }

        &:before {
          content: "";
          position: absolute;
          width: 40px;
          height: 4px;
          background-color: #ebe8df;
          bottom: 0;
          left: 0;

          @media #{$mobile} {
            left: 50%;
            transform: translateX(-50%);
          }
        }

        li {
          display: inline-block;
          position: relative;
          padding-left: 60px;
          margin-right: 40px;

          @media #{$mobile} {
            padding-left: 0;
            margin-right: 0;
            text-align: center;
          }

          @media #{$betweentabdesk} {
            margin-right: 30px;
          }

          .icon {
            width: 48px;
            height: 48px;
            position: absolute;
            top: 0;
            left: 0;

            @media #{$mobile} {
              margin: 0 auto 14px;
              line-height: 48px;
              position: relative;
            }
          }

          h3 {
            font-size: 24px;
            line-height: 20px;

            @media #{$mobile} {
              margin-bottom: 5px;
            }
          }

          span {
            font-size: 16px;
            line-height: 20px;
          }
        }
      }

      .authority-info-txt {
        font-size: 24px;
        line-height: 36px;
        position: relative;
        margin-bottom: 50px;
        background-image: url(../imgs/quote.svg);
        background-size: contain;
        background-repeat: no-repeat;

        @media #{$mobile} {
          background-image: none;
          font-size: 18px;
          line-height: 32px;
          text-align: center;
          margin-bottom: 40px;
        }
      }

      .person-info {
        position: relative;
        padding-left: 220px;

        @media #{$mobile} {
          padding-left: 0;
          text-align: center;
        }

        .jfl {
          position: absolute;
          top: 0;
          left: 0;

          @media #{$mobile} {
            position: relative;
            margin-bottom: 10px;
            transform: scale(0.75);
          }

          @media #{$betweentabdesk} {
            top: 6px;
          }
        }

        h3 {
          font-size: 24px;
          line-height: 20px;
          margin-bottom: 12px;

          @media #{$mobile} {
            margin-bottom: 10px;
          }
        }

        p {
          font-size: 16px;
          line-height: 20px;

          @media #{$mobile} {
            width: 80%;
            margin: auto;
          }
        }
      }
    }
  }

  &.homepage {
    padding-top: 0;
    background-color: #f9f8f4;

    .authority-content {
      padding-top: 0;
      padding-bottom: 5px;

      .authority-info {
        .authority-info-txt {
          font-size: 18px;
        }

        .person-info {

          >h3,
          p {
            display: inline-block;
            font-size: 16px;

            @media #{$mobile} {
              display: block;
            }
          }

          p {
            font-size: 13px;
            color: #8b898f;

            @media #{$mobile} {
              width: 100%;
            }
          }
        }

        ul {
          padding-bottom: 0;
          margin-bottom: 0;

          @media #{$maxTab} {
            display: block;
            padding-top: 25px;
          }

          @media #{$tablet} {
            padding-top: 0;
          }

          h3,
          span {
            font-size: 13px;
            display: inline-block;
          }

          &:before {
            display: none;
          }

          li {
            padding-left: 30px;
            margin-right: 18px;

            @media #{$mobile} {
              padding-left: 0;
              margin-right: 8px;

              .icon {
                margin-bottom: 6px;
              }
            }

            .icon {
              width: 24px;
              height: 24px;
              line-height: 24px;

              img {
                max-width: 100%;
              }
            }
          }
        }
      }

      @media #{$mobile} {
        padding-bottom: 25px;
        padding-top: 32px;

        .authority-img {
          padding: 0 50px;

          .authority-info {
            .authority-info-txt {
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}

/* End Authority */

/* Start Customers Features */
.customers-features {
  background-color: #fff;
  overflow: hidden;
  padding-top: 290px;

  @media #{$mobile} {
    padding-top: 355px;
  }

  .customer-feat {
    background-color: #f9f8f4;
    padding: 130px 60px;
    position: relative;

    @media #{$mobile} {
      padding: 40px 32px 60px;
      margin-bottom: 4px;
    }

    @media #{$tablet} {
      padding: 65px 32px 60px;
    }

    img {
      @media #{$mobile} {
        transform: scale(0.63);
      }

      @media #{$tablet} {
        transform: scale(0.8);
      }
    }

    h2 {
      margin: 58px 0 26px;
      font-size: 36px;
      line-height: 44px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        margin: 12px 0 16px;
      }

      @media #{$tablet} {
        font-size: 26px;
        line-height: 32px;
        margin: 20px 0;
      }
    }

    p {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 36px;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 20px;
      }
    }

    a {
      padding: 21px 46px;

      @media #{$maxTab} {
        padding: 24px 0;
        display: block;
      }

      @media (max-width: 350px) {
        padding: 24px 20px;
      }

      &:hover {
        background-color: $primaryColor;
        color: #fff;

        i {
          color: #fff;
        }
      }

      &:active {
        background-color: #ff5784;
      }
    }

    &.one {
      margin-right: 5px;

      @media #{$mobile} {
        margin-right: 0;
      }

      &:before {
        content: "";
        position: absolute;
        display: block;
        top: -60px;
        left: -60px;
        border-width: 60px;
        border-style: solid;
        border-color: transparent #fff transparent transparent;
        transform: rotate(45deg);

        @media #{$mobile} {
          top: -30px;
          left: -30px;
          border-width: 30px;
        }

        @media #{$tablet} {
          top: -40px;
          left: -40px;
          border-width: 40px;
        }
      }
    }

    &.two {
      padding-top: 118px;
      margin-left: 5px;

      @media #{$mobile} {
        padding: 30px 32px 60px;
        margin-left: 0;
      }

      @media #{$tablet} {
        padding: 40px 32px 60px;
      }

      &:before {
        content: "";
        position: absolute;
        display: block;
        bottom: -60px;
        right: -60px;
        border-width: 60px;
        border-style: solid;
        border-color: transparent transparent transparent #fff;
        transform: rotate(45deg);

        @media #{$mobile} {
          bottom: -30px;
          right: -30px;
          border-width: 30px;
        }

        @media #{$tablet} {
          bottom: -40px;
          right: -40px;
          border-width: 40px;
        }
      }

      h2 {
        margin-top: 38px;

        @media #{$mobile} {
          margin-top: -5px;
        }

        @media #{$tablet} {
          margin-top: 13px;
        }
      }

      a {
        background-color: $primaryColor;
        color: #fff;

        &:hover {
          background-color: #e33866;
        }

        &:active {
          background-color: #ff5784;
        }
      }
    }
  }
}

/* End Customers Features */