/* Start expired subscription */
.content-one.expired-subscription-page {
  background-color: #f1f1ed;
  padding-top: 20px;

  @media #{$tablet} {
    padding-top: 40px;
  }
}

.expired-subscription {
  .expiration-msg {
    padding: 50px 35px 50px 105px;
    background: rgba(239, 66, 115, 0.05);
    border: 1px solid #ef4273;
    border-radius: 5px;
    margin-top: 80px;
    margin-bottom: 20px;
    position: relative;

    @media #{$mobile} {
      padding: 22px 25px;
      margin-top: 0;
    }

    @media #{$tablet} {
      padding: 30px 35px 30px 75px;
      margin-top: 0;
    }

    img {
      position: absolute;
      left: 60px;

      @media #{$mobile} {
        left: 50%;
        transform: translateX(-50%);
        top: 22px;
      }

      @media #{$tablet} {
        left: 35px;
      }
    }

    p {
      font-size: 18px;
      line-height: 24px;
      font-family: "AvertaBold";
      color: $primaryColor;
      margin-bottom: 0;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 24px;
        margin-top: 35px;
        margin-bottom: 15px;
        text-align: center;
      }
    }

    a {
      position: absolute;
      display: inline-block;
      top: 35px;
      right: 35px;
      padding: 16px 53px;

      @media #{$mobile} {
        position: relative;
        top: 0;
        right: 0;
        width: 100%;
        display: block;
        font-size: 16px;
        text-align: center;
        padding: 14px;
      }

      @media #{$tablet} {
        padding: 16px 35px;
        top: 14px;
      }

      &:hover {
        color: #fff;
      }
    }
  }
}

/* End expired subscription */

/* Start wevpn benefits */
.wevpn-benefits {
  margin-bottom: 80px;

  @media #{$mobile} {
    margin-bottom: 20px;
  }

  @media #{$tablet} {
    margin-bottom: 40px;
  }

  .wevpn-benefits-content {
    padding: 60px 80px 35px;
    background-color: #fff;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;

    @media #{$mobile} {
      padding: 28px 21px 20px;
      box-shadow: none;
    }

    @media #{$tablet} {
      padding: 60px 40px 35px;
    }

    @media #{$betweentabdesk} {
      padding: 60px 30px 35px;
    }

    h2 {
      font-size: 24px;
      line-height: 32px;
      width: 40%;
      margin-bottom: 40px;

      @media #{$mobile} {
        font-size: 18px;
        line-height: 24px;
        width: 95%;
        margin-bottom: 25px;
      }

      @media #{$tablet} {
        width: 70%;
      }

      @media #{$betweentabdesk} {
        width: 49%;
      }
    }

    .wevpn-benefits-list {
      overflow: hidden;

      li {
        float: left;
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 25px;
        width: 33.3333%;
        color: #5a5761;

        @media #{$mobile} {
          width: 100%;
          float: none;
          margin-bottom: 20px;
        }

        @media #{$tablet} {
          width: 50%;
          margin-bottom: 20px;
        }

        i {
          color: #fff;
          font-size: 12px;
          width: 32px;
          height: 32px;
          background-color: #08ae7e;
          border-radius: 50%;
          text-align: center;
          line-height: 32px;
          margin-right: 5px;
        }
      }
    }
  }
}

/* End wevpn benefits */