
.servers-search {
  padding-top: 108px;
  padding-bottom: 213px;
  background-image: url(../imgs/map-grey.png);
  background-repeat: no-repeat;
  background-position: center;
  border-bottom: 1px solid #e0ded5;

  @media #{$mobile} {
    padding-top: 0;
    padding-bottom: 40px;
    background: none;
  }

  @media #{$tablet} {
    padding-top: 68px;
    padding-bottom: 160px;
    background-position: -10px 20%;
    background-size: contain;
  }

  .servers-search-content {
    @media #{$mobile} {
      background-image: url(../imgs/servers-map-small.png);
      background-size: 100% 70%;
      background-repeat: no-repeat;
      background-position: 0px 20px;
    }
  }

  h2 {
    margin-bottom: 35px;

    @media #{$mobile} {
      width: 70%;
      margin: 0 auto 20px;
      padding-top: 27px;
    }
  }

  .servers-list {
    width: 225px;
    margin: auto;
    margin-bottom: 36px;

    @media #{$mobile} {
      text-align: center;
      width: auto;
      margin-bottom: 20px;
    }

    li {
      font-size: 18px;
      line-height: 32px;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 14px;
        display: inline-block;
        margin-right: 12px;

        i {
          font-size: 16px;
        }
      }

      i {
        color: #08ae7e;
        margin-right: 7px;
      }
    }
  }

  form {
    border: 1px solid #ebe8df;
    box-sizing: border-box;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    position: relative;

    input {
      width: 100%;
      display: block;
      padding: 29px 40px;
      border: 0;
      font-size: 20px;

      @media #{$mobile} {
        height: 56px;
        font-size: 14px;
        line-height: 20px;
        padding: 19px 16px;
      }

      &::placeholder {
        font-size: 20px;
        line-height: 20px;

        @media #{$mobile} {
          font-size: 14px;
        }
      }
    }

    button {
      background-color: #fff;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      border: 0;
      cursor: pointer;
      width: 88px;
      height: 100%;

      @media #{$mobile} {
        width: 56px;

        svg {
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}

/* Start Americas */

body.servers-page {
  background: #f5f4f0;
}

.americas {
  margin-top: -114px;
  padding-bottom: 88px;

  @media #{$mobile} {
    margin-top: 24px;
    padding-bottom: 30px;
  }

  h2 {
    margin-bottom: 33px;
    font-size: 24px;
    line-height: 24px;
    color: #5b5863;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 16px;
    }
  }

  .table {
    background-color: #fff;
    border-color: #e0ded5;

    thead {
      tr {
        background-color: #f5f4f0;

        th {
          font-size: 12px;
          line-height: 12px;
          text-transform: uppercase;
          color: #8d8b92;
          font-family: "Averta";
          padding-bottom: 20px;
          height: 56px;
          border: 1px solid #e0ded5;

          &.tcp {
            padding-bottom: 14px;
          }

          &:first-child {
            padding-left: 32px;
            border-left: 0;
          }

          &:last-child {
            border-right: 0;
          }

          &:not(:first-child) {
            width: 191px;

            @media #{$tablet} {
              width: 106px;
            }

            @media #{$betweentabdesk} {
              width: 150px;
            }
          }
        }
      }
    }

    tbody {
      tr.child {
        background-color: #fbfaf9;
        display: none;

        th {
          color: #5b5863;
          padding-left: 67px;
        }
      }

      tr.parent.active {
        .arrow {
          span {
            background-color: #5b5863;

            &:first-of-type {
              transform: rotate(-45deg);
            }

            &:last-of-type {
              transform: rotate(45deg);
            }
          }
        }
      }

      th {
        position: relative;

        .arrow {
          cursor: pointer;
          top: 22px;
          right: 25px;
        }

        &:first-child {
          font-size: 14px;
          line-height: 64px;
          color: #5b5863;
          height: 64px;
          padding: 0;
          padding-left: 32px;
          border: 1px solid #e0ded5;

          img {
            padding-right: 7px;
            position: relative;
            top: -2px;
          }
        }
      }

      td {
        line-height: 64px;
        height: 64px;
        padding: 0;
        border: 1px solid #e0ded5;

        i {
          color: #08ae7e;
        }
      }

      tr {

        th:first-child,
        td:first-child {
          border-left: 0;
        }

        th:last-child,
        td:last-child {
          border-right: 0;
        }
      }
    }
  }

  .panel-continent {
    .panel-country {
      .panel-country-header {
        background-color: #ffffff;
        border-bottom: 1px solid #e0ded5;
        padding: 0 16px;
        position: relative;
        height: 48px;

        >span {
          font-size: 14px;
          line-height: 48px;
          font-family: "AvertaSemibold";

          img {
            margin-right: 3px;
          }
        }

        &.active {
          .arrow {
            span {
              background-color: #5b5863;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
      }

      .has-clusters {
        display: none;

        .panel-cluster {
          background-color: #fbfaf9;

          .panel-cluster-header {
            position: relative;
            border-bottom: 1px solid #e0ded5;
            padding: 0 47px;
            height: 36px;

            span {
              font-size: 14px;
              line-height: 36px;
            }

            .arrow {
              top: 10px;
            }

            &.active {
              .arrow {
                span {
                  background-color: #5b5863;

                  &:first-of-type {
                    transform: rotate(-45deg);
                  }

                  &:last-of-type {
                    transform: rotate(45deg);
                  }
                }
              }
            }
          }

          .panel-cluster-body {
            padding: 9px 47px;
            border-bottom: 1px solid #e0ded5;
            display: none;

            .panel-protocol {
              p {
                font-size: 14px;
                line-height: 32px;
                color: #8d8b92;

                i {
                  float: right;
                  color: #62d5b4;
                }
              }
            }
          }
        }
      }

      .panel-country-body {
        padding: 14px 16px;
        border-bottom: 1px solid #e0ded5;
        background-color: #fbfaf9;
        display: none;

        .panel-protocol {
          p {
            font-size: 14px;
            line-height: 32px;
            color: #8d8b92;

            i {
              float: right;
              color: #62d5b4;
            }
          }
        }
      }
    }
  }

  .europe-content {
    margin-top: 57px;
    margin-bottom: 57px;

    @media #{$mobile} {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
}

.table-bordered thead td,
.table-bordered thead th {
  border-bottom-width: 1px;
  border-color: #e0ded5;
}

.table-bordered td,
.table-bordered th {
  border-color: #e0ded5;
}

/* End Americas */

.devices-os.servers-page {
  background-color: #f9f8f4;
  padding-bottom: 154px;

  @media #{$mobile} {
    padding-bottom: 55px;

    .devices-title {
      width: 80%;
    }
  }

  @media #{$tablet} {
    padding-bottom: 95px;
  }

  @media #{$betweentabdesk} {
    padding-bottom: 20px;
  }
}

/* Start Servers Benefits */
.servers-benefits {
  padding: 134px 0 155px;
  background-color: #f9f8f4;

  @media #{$maxTab} {
    padding: 60px 0;
  }

  @media #{$betweentabdesk} {
    padding: 100px 0;
  }

  h2 {
    width: 50%;
    margin: 0 auto 60px;

    @media #{$mobile} {
      width: 100%;
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 28px;
      padding-left: 20px;
    }

    @media #{$tablet} {
      width: 75%;
      font-size: 30px;
      margin-bottom: 30px;
    }

    @media #{$betweentabdesk} {
      width: 65%;
    }
  }

  .servers-benefits-list {
    @media #{$mobile} {
      padding-left: 20px;
    }

    li {
      float: left;
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 25px;
      color: #5a5761;

      @media #{$mobile} {
        font-size: 16px;
        line-height: 20px;
        width: 100%;
        margin-bottom: 20px;
      }

      @media #{$tablet} {
        font-size: 17px;
        margin-bottom: 20px;
      }

      &:nth-child(odd) {
        width: 60%;

        @media #{$mobile} {
          width: 100%;
        }
      }

      &:nth-child(even) {
        width: 40%;

        @media #{$mobile} {
          width: 100%;
        }
      }

      i {
        color: #fff;
        font-size: 21px;
        width: 48px;
        height: 48px;
        background-color: #08ae7e;
        border-radius: 50%;
        text-align: center;
        line-height: 48px;
        position: relative;
        top: 5px;
        margin-right: 5px;

        @media #{$maxTab} {
          width: 32px;
          height: 32px;
          line-height: 32px;
          font-size: 12px;
          top: 0;
        }
      }
    }
  }

  .benefits-btn {
    text-align: center;
    margin-top: 60px;

    @media #{$mobile} {
      margin-top: 8px;
    }

    @media #{$tablet} {
      margin-top: 30px;
    }

    a {
      padding: 24px 96px 23px;

      @media #{$mobile} {
        display: block;
        padding: 24px 0 23px;
      }

      &:hover {
        background-color: $primaryColor;
        color: #fff;

        i {
          color: #fff;
        }
      }
    }
  }
}

/* End Servers Benefits */