/* Start Footer */
footer {
  background-color: #fff;
  padding-top: 100px;
  border-top: 1px solid #EBE8DF;

  &.footer-home {
    border-top: 0;
  }

  @media #{$maxTab} {
    padding-top: 60px;
  }

  .footer-content {
    text-align: center;

    .footer-logo {
      img {
        user-select: none;

        @media #{$mobile} {
          transform: scale(0.75);
        }
      }
    }

    .apps {
      margin-top: 38px;
      margin-bottom: 30px;
      font-size: 18px;
      line-height: 28px;

      @media #{$mobile} {
        margin-top: 28px;
        margin-bottom: 12px;
      }
    }

    .footer-icons {
      margin-bottom: 90px;

      @media #{$mobile} {
        margin-bottom: 40px;
      }

      @media #{$tablet} {
        margin-bottom: 60px;
      }

      li {
        display: inline-block;
        margin: 0 20px;
        width: 40px;
        height: 40px;

        @media #{$mobile} {
          margin: 0;
          margin-right: 12px;
          width: 25px;
          height: 25px;
        }

        @media (max-width: 350px) {
          margin-right: 8px;
        }

        @media #{$tablet} {
          margin: 0 10px;
        }

        a {
          display: block;

          &:hover {
            svg {
              path {
                fill: $primaryColor;
              }
            }

            .firefox {
              .line {
                stroke: $primaryColor;
                fill: none;
              }

              .fox {
                fill: $primaryColor;
              }
            }
          }

          svg {
            path {
              transition: all 0.3s ease-in-out;
            }

            @media #{$mobile} {
              transform: scale(0.7);
            }

            @media #{$tablet} {
              transform: scale(0.85);
            }

            @media (max-width: 350px) {
              transform: scale(0.6);
            }
          }
        }
      }
    }
  }

  .copyright {
    border-top: 1px solid #ebe8df;
    padding-top: 39px;
    padding-bottom: 39px;
    font-size: 18px;
    line-height: 28px;

    @media #{$mobile} {
      padding-top: 32px;
      padding-bottom: 60px;
    }

    @media #{$tablet} {
      text-align: center;
    }

    .copyright-txt {
      @media #{$mobile} {
        text-align: center;

        p {
          font-size: 13px;
          line-height: 13px;
        }
      }

      p {
        margin-bottom: 0;
      }
    }

    .copyright-links {
      @media #{$mobile} {
        text-align: center !important;
        margin-bottom: 17px;
      }

      @media #{$tablet} {
        text-align: left;
        margin-bottom: 20px;
      }

      li {
        display: inline-block;
        margin-left: 0;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 28px;
          margin-left: 5px;
        }

        @media #{$tablet} {
          padding: 0;
        }

        &:last-child {
          a:last-child {
            padding-right: 0;
          }
        }

        a {
          padding: 0 15px;
          display: inline-block;
          transition: color 0.3s ease-in-out;
          text-decoration: none;

          @media #{$mobile} {
            padding: 0 8px;
          }

          @media #{$tablet} {
            padding: 0 14px;
          }

          @media #{$betweentabdesk} {
            padding: 0 7px;
          }

          &:hover {
            color: $primaryColor;
          }
        }
      }
    }
  }
}

/* End Footer */