/* Start login */
div.login-page,
div.contact-us {
  background-image: url(../imgs/login-bg.png);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  padding: 60px 0 80px;

  @media #{$mobile} {
    background-size: 120%;
    background-position: 50% -10px;
    padding: 20px 0 40px;
  }

  @media #{$tablet} {
    background-size: 110%;
    background-position: 50% 0;
  }

  @media #{$betweentabdesk} {
    background-size: 80%;
    background-position: 50% 0;
  }

  .padd {
    padding-right: 5px;
    padding-left: 5px;

    @media #{$mobile} {
      padding-right: 15px;
      padding-left: 15px;
    }

    @media #{$betweentabdesk} {
      padding-right: 0;
      padding-left: 0;
    }
  }

  h1 {
    margin-bottom: 40px;
    font-size: 36px;
    line-height: 56px;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 20px;
    }

    @media #{$tablet} {
      margin-bottom: 30px;
    }
  }

  .login-content,
  .contact-us-content {
    background: #ffffff;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    padding: 80px 60px 60px;

    @media #{$mobile} {
      padding: 24px;
    }

    @media #{$tablet} {
      padding: 50px;
    }

    @media #{$betweentabdesk} {
      padding: 60px 50px 50px;
    }

    label {
      margin-bottom: 4px;

      @media #{$mobile} {
        margin-bottom: 0;
        line-height: 1.5;
      }
    }

    input,
    select,
    textarea {
      border: 0;
      outline: none;
      box-shadow: none;
      padding-left: 0;
      padding: 0;
      height: auto;

      @media #{$mobile} {
        font-size: 14px;
      }

      &::placeholder {
        font-size: 16px;
        line-height: 20px;

        @media #{$mobile} {
          font-size: 14px;
        }
      }
    }

    .form-group {
      border: 1px solid #e0ded5;
      padding: 15px 30px;
      margin-bottom: 20px;

      @media #{$mobile} {
        padding: 5px 16px 9px;
        margin-bottom: 8px;
      }

      label {
        font-size: 12px;
        font-family: "AvertaSemibold";
        margin-bottom: 0;
      }
    }

    .forgot-password {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media #{$mobile} {
        flex-direction: column;
      }

      a {
        font-size: 14px;
        line-height: 20px;
        color: $primaryColor;
        text-decoration: underline !important;
        transition: color 0.3s ease-in-out;

        @media #{$mobile} {
          order: 2;
          font-size: 12px;
        }

        &:hover {
          color: #e33866;
          text-decoration: none !important;
        }
      }

      button {
        padding: 16px 58px 15px;

        @media #{$mobile} {
          font-size: 16px;
          width: 100%;
          display: block;
          padding: 16px 0 15px;
          margin-bottom: 11px;
        }
      }
    }

    &.reset-password-page {
      padding: 60px;

      @media #{$mobile} {
        padding: 24px;
      }

      .reset {
        display: block;
        width: 100%;
        margin-bottom: 20px;
      }

      p {
        font-size: 13px;
        line-height: 14px;
        color: #8d8b92;
        text-align: center;

        a {
          font-size: 14px;
        }
      }
    }
  }

  .login-footer {
    text-align: center;
    margin-top: 30px;

    @media #{$mobile} {
      margin-top: 20px;
    }

    p {
      font-size: 14px;
      line-height: 17px;
      color: #8d8b92;
    }
  }

  &.reset-password-page {
    @media #{$mobile} {
      background-position-y: 10%;
    }

    @media (min-width: 768px) {
      background-position-y: 50px;
    }
  }
}

/* End login */