// Start Upgrade Plan page
.dashboard.upgrade-plan-page {
  h2 {
    @media #{$mobile} {
      width: 60%;
      margin: 0 auto 28px;
    }
  }

  .payment-features {
    text-align: center;
    margin-top: -30px;
    margin-bottom: 50px;

    @media #{$mobile} {
      text-align: left;
      margin-left: 80px;
      margin-bottom: 30px;
    }

    li {
      display: inline-block;

      @media #{$mobile} {
        font-size: 14px;
        margin-bottom: 10px;
      }

      @media #{$tablet} {
        font-size: 15px;
      }

      &:not(:last-child) {
        margin-right: 24px;

        @media #{$tablet} {
          margin-right: 15px;
        }
      }

      svg {
        margin-right: 5px;

        @media #{$tablet} {
          position: relative;
          top: -1px;
          margin-right: 0;
        }
      }
    }
  }
}

.list-content.upgrade-plan-page {
  >div {
    display: none;
  }
}

.upgrade-plan-content {
  .pricing-tables {
    padding-top: 80px;
    padding-bottom: 60px;

    @media #{$mobile} {
      padding-top: 20px;
      padding-bottom: 29px;
    }

    @media #{$tablet} {
      padding-top: 60px;
      padding-bottom: 30px;
    }

    .price-card {
      label {
        padding-bottom: 0;

        @media #{$mobile} {
          padding-bottom: 24px;
        }
      }
    }
  }

  .payment {
    margin-top: 28px;

    @media #{$mobile} {
      margin-top: 0;

      .row {
        position: relative;

        .plan-details-col {
          position: absolute;
          right: 0;
          left: 0;
          top: 182px;
        }
      }
    }

    hr {
      height: 1px;
      width: 100%;
      background-color: #e3e0d3;
      margin-top: 15px;
      margin-bottom: 32px;
      border: 0;

      @media #{$mobile} {
        display: none;
      }
    }

    .payment-methods {
      padding-bottom: 80px;

      @media #{$mobile} {
        padding-bottom: 35px;
      }

      h3 {
        font-size: 24px;
        line-height: 44px;
        padding-top: 16px;

        @media #{$mobile} {
          padding-top: 0;
          font-size: 18px;
          line-height: 24px;
          margin-bottom: 20px;
        }

        @media #{$tablet} {
          font-size: 22px;
          line-height: 34px;
        }
      }

      .order-now {
        @media #{$mobile} {
          margin-top: 385px;
        }

        button {
          padding: 16px 54px;
          margin-top: 32px;
          margin-bottom: 32px;

          @media #{$mobile} {
            width: 100%;
            display: block;
            font-size: 16px;
            margin-top: 12px;
            margin-bottom: 12px;
          }
        }
      }

      .back-btn {
        @media #{$mobile} {
          text-align: center;
        }

        .back {
          font-size: 20px;
          line-height: 25px;
          font-family: "AvertaSemibold";
          color: $primaryColor;

          @media #{$mobile} {
            font-size: 16px;
          }

          &:hover {
            color: $primaryColor;
          }
        }
      }
    }

    .plan-details {
      .plan-details-content {
        background-color: #fff;

        h3 {
          font-size: 24px;
          line-height: 44px;
          padding-left: 60px;
          padding-top: 16px;

          @media #{$mobile} {
            font-size: 18px;
            line-height: 24px;
            padding-left: 16px;
            margin-bottom: 0;
          }

          @media #{$tablet} {
            font-size: 22px;
            line-height: 34px;
            padding-left: 20px;
          }
        }

        .line {
          margin-bottom: 20px;

          @media #{$mobile} {
            display: block;
            margin-bottom: 10px;
          }
        }

        .plan-details-list {
          padding-left: 60px;
          padding-right: 60px;

          @media #{$mobile} {
            padding-left: 16px;
            padding-right: 16px;
          }

          @media #{$tablet} {
            padding-left: 20px;
            padding-right: 20px;
          }

          li {
            padding-top: 6px;
            padding-bottom: 15px;
            color: #8d8b92;
            line-height: 28px;

            @media #{$mobile} {
              font-size: 14px;
              padding-bottom: 10px;
            }

            &:not(:last-child) {
              border-bottom: 1px dashed rgba(#e3e0d3, 0.7);
            }

            span {
              float: right;
              color: #5b5863;
            }

            .month {
              color: $primaryColor;
            }
          }
        }

        .total {
          margin-left: 60px;
          margin-right: 60px;
          padding-top: 26px;
          padding-bottom: 40px;
          color: #8d8b92;
          border-top: 2px solid rgba(#e3e0d3, 0.7);

          @media #{$mobile} {
            margin-left: 16px;
            margin-right: 16px;
            font-size: 14px;
            padding-top: 16px;
            padding-bottom: 24px;
          }

          @media #{$tablet} {
            margin-left: 20px;
            margin-right: 20px;
          }

          span {
            font-family: "AvertaSemibold";
          }

          .value {
            float: right;
            color: #5b5863;
          }
        }
      }

      .subscription-period {
        padding: 28px 60px;
        border: 1px solid #e3e0d3;
        border-radius: 5px;
        font-size: 16px;
        line-height: 28px;
        color: #8d8b92;
        margin-top: 20px;

        @media #{$mobile} {
          font-size: 14px;
          padding: 13px 38px;
          text-align: center;
          line-height: 20px;
          margin-top: 12px;
          margin-bottom: 12px;
        }

        @media #{$tablet} {
          padding: 10px 20px;
        }

        @media #{$betweentabdesk} {
          padding: 15px 60px;
        }
      }
    }
  }
}